.view-options-list .view-item {
  display: flex;
  min-height: 40px;
  padding: 4px 0;
  border-radius: 3px;
  font-size: 14px;
  color: #555;
  font-weight: 500;
  align-items: center;
}

.view-options-list .view-item:hover {
  background-color: #f5f5f5;
  cursor: pointer;
}

.view-options-list .view-item.selected-view-item {
  background-color: #e9e9e9;
}

.view-options-list .is-selected {
  width: 30px;
  text-align: center;
}

.view-options-list .view-item-content {
  flex: 1;
  line-height: 32px;
}

.view-options-list .dtable-font {
  font-size: 14px;
}

.view-options-list .view-item-content .dtable-font {
  margin-right: 6px;
}

.view-options-list .is-selected .dtable-icon-check-mark {
  font-size: 12px;
  color: #999;
}