.statistic-records-dialog {
  width: 800px;
  max-width: 800px;
  height: calc(100% - 56px);
}

.statistic-records-dialog .modal-content {
  width: 100%;
  height: 100%;
}

.statistic-records-dialog .search-container {
  height: 57px;
  padding: 0 20px;
  border-bottom: 0;
  background-color: #f8f8f8;
}

.no-search-result {
  height: 100%;
  width: 100%;
  text-align: center;
  background: url('../img/search-empty-status.png') no-repeat;
  background-position: 310px 140px;
  background-size: 132px 132px;
  line-height: 600px;
  color: #aaa;
}

.statistic-records-dialog .search-container .modal-title {
  width: 100%;
}

.statistic-records-dialog .search-container .search-input-container {
  margin-top: 17px;
  height: 40px;
  position: relative;
}

.search-input-container .search-tables-input {
  width: 100%;
  height: 38px;
  line-height: 46px;
  border: unset;
  border-bottom: 2px solid #ededed;
  border-radius: unset;
  padding: 0 40px 0px 38px;
  background-color: #f8f8f8;
}

.search-input-container .search-tables-input:focus {
  outline: none;
  box-shadow: unset;
}

.search-input-container .search-icon {
  position: absolute;
  left: 0px;
  margin-left: 3px;
  line-height: 38px;
  text-align: center;
  color: #aaa;
}

.search-input-container .clear-search-text {
  display: inline-block;
  position: absolute;
  right: 30px;
  top: 10px;
  height: 12px;
  width: 12px;
  line-height: 12px;
  color: #ccc;
}

.search-input-container .clear-search-text .dtable-font {
  font-size: 12px;
}

.search-input-container .clear-search-text:hover {
  cursor: pointer;
}

.statistic-records-container {
  width: 100%;
  height: calc(100% - 57px);
  background-color: #f8f8f8;
  padding: 0;
}

.statistic-records-container .records-list {
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  padding: 10px 20px;
}

.statistic-records-container .records-list .records-container {
  height: auto;
  width: 100%;
}

.statistic-records-container .records-list::-webkit-scrollbar {
  width: 0;
}
