.dtable-dropdown-menu {
  cursor: pointer;
}

.dtable-dropdown-menu .toggle-icon {
  font-size: 14px;
  color: #999;
}

.dtable-dropdown-menu .toggle-icon:hover {
  font-size: 14px;
  color: #000;
}

.dtable-dropdown-menu .toggle-icon.small {
  display: inline-block;
  font-size: 12px;
  color: #949494;
  -webkit-transform: scale(.8);
  transform: scale(.8);
  -webkit-transition: all .1s;
  transition: all .1s;
}

.dtable-dropdown-menu .toggle-icon.small:hover {
  color: #999;
}

.dtable-dropdown-menu .dropdown-item {
  padding: 3px 12px;
  line-height: 1.5;
  font-size: 14px;
  color: #202428;
  cursor: pointer;
}

.dtable-dropdown-menu .dropdown-item:hover {
  color: #fff;
  background-color: #20a0ff;
}

.dtable-dropdown-menu .dropdown-item .item-icon {
  font-size: 14px;
  margin-right: 10px;
  color: #8c8c8c;
}

.dtable-dropdown-menu .dropdown-item:hover .item-icon {
  color: #fff;
}

.dtable-dropdown-menu .dropdown-toggle .item-text {
  width: calc(100% - 30px);
  display: inline-block;
}

.dtable-dropdown-menu .dropdown-toggle::after {
  color: #8c8c8c;
}

.dtable-dropdown-menu .dropdown-toggle:hover::after {
  color: #fff;
}

.dtable-dropdown-menu.large {
  margin: 0;
}

.dtable-dropdown-menu.large .dropdown-item {
  min-height: 32px;
}

.dtable-dropdown-menu .dropdown-item.disabled,
.dtable-dropdown-menu .dropdown-item.disabled .item-icon,
.dtable-dropdown-menu .dropdown-item.disabled:hover .item-icon,
.am-action-sheet-button-list-item .my-am-action.disabled,
.am-action-sheet-button-list-item .my-am-action.disabled .dtable-font {
  color: #c2c2c2;
}

.dtable-dropdown-menu .dropdown-item.disabled:hover {
  background-color: unset;
  cursor: default;
  color: #c2c2c2;
}

.dtable-dropdown-menu .no-options {
  min-height: 32px;
  color: #c2c2c2;
}