.single-selects-editor-list,
.multiple-selects-editor-list {
  min-width: 200px;
  padding: 0;
  min-height: 160px;
  overflow: hidden;
  opacity: 1;
  background-color: #ffffff;
  border: 1px solid #dedede;
  border-radius: 4px;
  box-shadow: 0px 2px 10px 0 #dedede;
}

.single-selects-editor-list .single-selects-container,
.multiple-selects-editor-list .multiple-selects-container {
  max-height: 200px;
  min-height: 100px;
  padding: 10px;
  overflow: auto;
}

.single-selects-editor-list .search-single-selects,
.multiple-selects-editor-list .search-multiple-selects {
  padding: 10px 10px 0 10px;
}

.single-selects-editor-list .search-single-selects input,
.multiple-selects-editor-list .search-multiple-selects input {
  max-height: 30px;
}

.single-selects-editor-list .single-select-item,
.multiple-selects-editor-list .multiple-select-item {
  display: flex;
  align-items: center;
  padding: 0;
}

.single-selects-editor-list .single-select-item:last-of-type,
.multiple-selects-editor-list .multiple-select-item:last-of-type {
  margin-bottom: 15px;
}

.single-selects-editor-list .single-select-item:hover,
.multiple-selects-editor-list .multiple-select-item:hover {
  background: none;
}

.single-selects-editor-list .single-select-container,
.multiple-selects-editor-list .multiple-select-container {
  display: flex;
  align-items: center;
  width: 100%;
  height: 30px;
  border-radius: 2px;
  font-size: 13px;
  color: #202428;
  padding-left: 12px;
}

.single-selects-editor-list .single-select-container:hover,
.multiple-selects-editor-list .multiple-select-container:hover {
  background: #f5f5f5;
  cursor: pointer;
}

.single-selects-editor-list .single-select,
.multiple-selects-editor-list .multiple-select {
  height: 30px;
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}

.single-select-avatar,
.multiple-select-avatar {
  border-radius: 50%;
  vertical-align: middle;
}

.single-selects-editor-list .single-select-avatar,
.multiple-selects-editor-list .multiple-select-avatar {
  width: 16px;
  height: 16px;
  line-height: 16px;
  margin-left: 10px;
}

.single-selects-editor-list .single-select-name,
.multiple-selects-editor-list .multiple-select-name {
  display: inline-block;
  padding: 0px 10px;
  height: 20px;
  line-height: 20px;
  text-align: center;
  margin-left: 0;
  border-radius: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 5px;
}

.single-selects-editor-list .single-select-check-icon,
.multiple-selects-editor-list .multiple-select-check-icon {
  width: 20px;
  text-align: center;
}

.single-selects-editor-list .single-select-check-icon .dtable-font,
.multiple-selects-editor-list .multiple-select-check-icon .dtable-font {
  font-size: 12px;
  color: #798d99;
}

.single-selects-editor-list .none-search-result,
.multiple-selects-editor-list .none-search-result {
  font-size: 14px;
  opacity: .5;
  display: inline-block;
}

.single-selects-editor-list .add-search-result,
.multiple-selects-editor-list .add-search-result {
  height: 30px;
  padding: 0 10px;
  overflow: hidden;
}

.single-selects-editor-list .add-search-result:hover,
.multiple-selects-editor-list .add-search-result:hover {
  cursor: pointer;
  background-color: #f0f0f0;
}
.single-selects-editor-list .add-search-result .add-new-option,
.multiple-selects-editor-list .add-search-result .add-new-option {
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}