.mobile-checkbox-editor-container,
.checkbox-editor-container {
  width: 100%;
  height: 100%;
  outline: 2px solid #66afe9;
  background-color: #fff;
  box-sizing: border-box;
}

.checkbox-formatter-container .show-box,
.mobile-checkbox-editor-container .show-box,
.mobile-checkbox-editor-container .checkbox {
  display: inline;
  vertical-align: middle; 
  margin-left: 50%;
  transform: translate(-10px, 0);
  box-shadow: none;
  outline: none;
}

.react-grid-Cell .checkbox,
.checkbox-formatter-container .checkbox,
.checkbox-formatter-container .checkbox {
  width: 20px;
  height: 20px;
  display: inline;
  vertical-align: middle;
  margin-left: 50%;
  transform: translate(-10px, 0);
  box-shadow: none;
  outline: none;
}

.checkbox-formatter-container,
.mobile-checkbox-editor-container {
  position: relative;
}

.checkbox-formatter-container .checkbox,
.mobile-checkbox-editor-container .checkbox {
  opacity: 0;
}

.checkbox-formatter-container input:checked + .show-box,
.mobile-checkbox-editor-container input:checked + .show-box {
  background: #3b99fc;
}

.checkbox-formatter-container .show-box,
.mobile-checkbox-editor-container .show-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 16px;
  height: 16px;
  border-radius: 2px;
  border: 1px solid #d8d8d8;
  background: white;
}

.checkbox-formatter-container .show-box:before,
.mobile-checkbox-editor-container .show-box:before {
  content: '';
  position: absolute;
  top: 2px;
  left: 4px;
  width: 5px;
  height: 8px;
  border: solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

.checkbox-formatter-container .show-box {
  margin: 4px 52%;
}

.mobile-checkbox-editor-container .show-box {
  margin: 9px 52%;
}

.rdg-editor-container .checkbox-editor-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.row-expand-item .checkbox-editor-container {
  width: 20px;
  height: 20px;
}

.checkbox-editor-container .checkbox {
  top: 0;
  left: 0;
  opacity: 0;
}

.rdg-editor-container .checkbox-editor-container .checkbox {
  width: 100%;
  height: 100%;
}

.row-expand-item .checkbox-editor-container .checkbox {
  width: 16px;
  height: 16px;
}

.checkbox-editor-container .check-mark {
  width: 20px;
  height: 20px;
  display: inline-block;
  box-shadow: none;
  outline: none;
}

.row-expand-item .checkbox-editor-container .check-mark {
  margin-left: -2px;
}

.rdg-editor-container .checkbox-editor-container .check-mark {
  margin-top: 5px;
}