.radio-custom {
    opacity: 0;
    position: absolute;
}

.react-grid-checkbox-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 8px;
}

.react-grid-checkbox {
  position: absolute;
  width: 12px;
  height: 12px;
  margin: 0 !important;
  opacity: 1;
  left: 10px;
}

.react-grid-checkbox, .react-grid-checkbox-label, .radio-custom, .radio-custom-label {
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
}

.react-grid-checkbox-label, .radio-custom-label {
    position: relative;
}

.react-grid-checkbox + .react-grid-checkbox-label:before, .radio-custom + .radio-custom-label:before {
    content: '';
    background: #fff;
    border: 2px solid #ddd;
    display: inline-block;
    vertical-align: middle;
    width: 14px;
    height: 14px;
    text-align: center;
}

.react-grid-checkbox:checked + .react-grid-checkbox-label:before {
    background: #005295;
    box-shadow: inset 0px 0px 0px 0px #fff;
}

.react-grid-checkbox:focus + .react-grid-checkbox-label, .radio-custom:focus + .radio-custom-label {
  outline: 1px solid #ddd; /* focus style */
}

.react-grid-HeaderCell input[type='checkbox'] {
  z-index: 99999;
}
.react-grid-HeaderCell > .react-grid-checkbox-container {
    padding: 0px 10px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}


.react-grid-HeaderCell > .react-grid-checkbox-container > .react-grid-checkbox-label {
   margin : 0;
   position: relative;
   font-size: 0;
}

.radio-custom + .radio-custom-label:before {
    border-radius: 50%;
}

.radio-custom:checked + .radio-custom-label:before {
    background: #ccc;
    box-shadow: inset 0px 0px 0px 4px #fff;
}

.checkbox-align {
    text-align: center;
}

.header-lock-box {
    height: 24px;
    width: 24px;
    position: relative;
    left: 60%;
    top: 4px;
    color: #aaa;
    z-index: 1001;
}

.header-lock-box .header-lock {
    font-size: 14px;
}

.header-lock-box .header-lock-container {
    height: 100%;
    width: 100%;
    text-align: center;
    line-height: 24px;
}

.header-lock-box .header-lock-container:hover {
    cursor: pointer;
    color: rgb(85, 85, 85);
}