.mobile-deleted-rows-view {
  min-height: 100%;
  width: 100%;
  background-color: #F3F3F3;
  position: fixed;
  left: 0;
  top: 0;
}

.deleted-rows-view {
  width: 100%;
  padding: 0 16px;
  overflow-y: scroll;
}

.deleted-rows-view::-webkit-scrollbar {
  width: 0;
}

.deleted-rows-content-view {
  height: auto;
  width: 100%;
}

.deleted-rows-view .deleted-row-view {
  height: 56px;
  width: 100%;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e9ecef;
  padding: 8px 3px;
  font-size: 14px;
}

.deleted-rows-view .deleted-row-view .op-user-img-view {
  width: 15%;
}

.deleted-rows-view .deleted-row-view .deleted-row-content-view {
  width: 85%;
}

.deleted-rows-view .deleted-row-view .deleted-row-time {
  width: 100%;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
}

.deleted-rows-view .deleted-row-view .deleted-row-op-user {
  display: inline-block;
  max-width: 70%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding-right: 20px;
}

.deleted-rows-view .deleted-row-view .deleted-row-op-time {
  display: inline-block;
  max-width: 30%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #929292;
}

.deleted-rows-view .deleted-row-view .deleted-row-description-view {
  width: 100%;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
}

.deleted-row-description-view .description {
  display: inline-block;
  width: 80%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding-right: 20px;
}

.deleted-row-description-view .restore {
  display: inline-block;
  width: 20%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: right;
  color: #F09F3F;
}