.image-formatter {
  padding: 2px 0;
  display: inline-block;
}

.image-formatter img {
  max-height: 28px;
  margin-right: 4px;
  border: 1px solid #f0f0f0;
}

.image-formatter img:hover {
  border: 1px solid #c9c9c9;
}

.image-editor-modal {
  width: 560px;
  overflow: hidden;
}

.image-editor-container {
  width: 100%;
  background-color: #fff;
}

.image-editor-container .image-previewer-container {
  position: relative;
  width: 100%;
  min-height: 30px;
}

.image-previewer-wrapper {
  padding: 0 20px;
  flex-direction: row;
  height: 350px;
  overflow-y: auto;
}

.image-previewer-content {
  display: flex;
  width: 518px;
  flex-wrap: wrap;
}

.image-previewer-box:nth-child(3n) {
  margin-right: 0;
}

.image-previewer-box {
  margin-right: 19px;
  width: 160px;
  height: 160px;
  margin-top: 15px;
  border: 2px solid #ededed;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;
}

.image-footer-choice {
  height: 30px;
  width: 100%;
  border-radius: 2px;
  border-top-right-radius: 0;
  border-top-left-radius: 0;
  bottom: 0;
  position: absolute;
  color: #fff;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  padding:0 3px;
  justify-content: space-between;
  align-items: center;
}

.image-footer-choice .image-footer-title {
  width: 68%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.image-footer-choice .image-footer-icon {
  display: flex;
  justify-content: center;
  align-items: center
}

.image-footer-choice .image-footer-icon span {
  cursor: pointer;
  display: flex;
  width: 24px;
  height: 24px;
  justify-content: center;
  align-items: center;
  color: #dbdbdb;
}

.image-footer-choice .image-footer-icon span:hover {
  color: #fff
}

.image-footer-choice .image-footer-icon span i {
  font-size: 14px;
  display: inline-block;
}

.image-previewer-box img {
  max-height: 100%;
  max-width: 100%;
}

.button-content {
  display: inline-block;
  user-select: none;
}

.image-addition-container {
  display: flex;
  flex-direction: row;
}

.image-addition-left {
  border-right: 1px solid #e9ecef;
  width: 142px;
  padding: 16px 0 10px 16px;
}

.image-addition-left .image-addition-nav {
  padding-right: 10px;
}

.image-addition-left .addition-item {
  padding: 5px 0 5px 12px;
  border-radius: 3px;
  display: inline-block;
  cursor: pointer;
  width: 100%;
}

.image-addition-left .addition-item:hover {
  background-color: #feefb8;
}

.image-addition-container .image-addition-left .selected-additoin-item {
  background-color: #f3af7d;
  color: #fff;
}

.upload-image {
  width: 1px;
  height: 1px;
  opacity: 0;
  position: absolute;
  top: 0;
  font-size: 0;
  cursor: pointer;
}

.image-addition-right {
  height: 350px;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 16px 16px;
  width: 416px;
}

.image-addition-right .image-addition-right-container {
  display: flex;
  flex-wrap: wrap;
  width: 384px;
  height: 100%;
}

.image-addition-right .image-upload-container {
  height: 100%;
  width: 100%;
  border: 1px dashed transparent;
  position: relative;
  cursor: pointer;
}

.image-addition-right .image-upload-container:hover .dtable-icon-add-files {
  color: orange;
}

.image-addition-right .image-upload-container-active {
  border: 1px dashed #ccc;
}

.image-addition-right .image-upload-container .image-add-span {
  margin-top: 5px;
  font-size: 12px;
}

.image-addition-right .image-upload-container .image-addition-paste {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  pointer-events: none;
  opacity: 0;
  z-index: 3;
}

.image-tip-addition {
  text-align: center;
  padding: 6rem 4rem;
  width: 100%;
}

.image-drop-active .dtable-font {
  color: orange;
}

.image-add-icon {
  line-height: 1;
}

.image-add-icon i {
  font-size: 54px;
  color: #ccc;
}

.image-editor-dialog .dtable-icon-return {
  color: #777777;
  font-size: 14px;
}

.image-upload-percent {
  width: 100%;
  height: 100%;
  border-radius: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.image-upload-success {
  width: 60px;
  height: 60px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 2px;
  position: absolute;
  z-index: 5;
}

.image-upload-success .image-upload-success-scale {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.image-upload-success .image-upload-success-scale .image-upload-success-tip {
  color: #fff;
  font-size: 12px;
  width: 100%;
  transform: scale(0.8);
  text-align: center;
}

.image-upload-success .image-upload-success-icon i {
  display: inline-block;
  color: #757474;
}

.image-upload-mask {
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 2;
  border-radius: 2px;
}

.image-upload-error-tip {
  z-index: 3;
  font-size: 12px;
  transform: scale(0.7);
  position: absolute;
  color: #fff;
  white-space: nowrap;
  text-align: center;
}

.image-upload-again {
  text-decoration: underline;
  cursor: pointer;
  margin-left: 3px;
}

.image-addition-right .image-wrapper {
  height: 120px;
  width: 120px;
  border: 1px solid #ededed;
  border-radius: 4px; 
  position: relative;
  margin-right: 12px;
  margin-bottom: 12px;
}

.image-addition-right .image-wrapper:nth-child(3n) {
  margin-right: 0px;
}

.image-addition-right .image-wrapper .image-content {
  width: 118px;
  height: 118px;
  text-align: center;
  line-height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-addition-right .image-wrapper img {
  max-height: 118px;
  max-width: 118px;
}

.image-addition-right .image-wrapper .image-add-box {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  flex-direction: column;
  font-size: 12px;
  cursor: pointer;
}

.image-addition-right .image-wrapper .image-add-button {
  position: relative;
  cursor: pointer;
}

.image-addition-right .image-wrapper .image-add-button i {
  color: #ccc;
  font-size: 50px;
}

.image-addition-right .image-wrapper .image-wrapper-drop-active i {
  color: orange;
}

.image-addition-right .image-wrapper:hover .image-add-button i {
  color:  orange;
}

.image-addition-right .image-wrapper .image-add-span {
  margin-top: 5px;
  font-size: 12px;
}

.delete-image-icon {
  position: absolute;
  top: -4px;
  right: -4px;
  height: 32px;
  width: 32px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  text-align: center;
  cursor: pointer;
  transform: scale(0.5)
}

.delete-image-icon .image-delete-span {
  font-size: 20px;
}

.image-link {
  width: 100%;
  padding: 6rem 0;
}

.image-link-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-link-input {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  width: 50%;
}

.image-link-input:focus {
  z-index: 2;
}

.image-link-icon {
  display: flex;
  width: 2.375rem;
  height: 2.375rem;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(0, 40, 100, 0.12);
  border-left: none;
  color: #a6aeb6;
  cursor: pointer;
  border-radius: 0 3px 3px 0;
  background: #fafafa;
}

.image-link-icon:hover {
  background: #ededed;
}

.image-link-error-message {
  margin-top: 10px;
  text-align: center;
  color: red;
}