.search-row {
  width: 100%;
  height: 88px;
  margin-top: 10px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.1);
  padding: 12px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.search-row:first-child {
  margin-top: 0;
}

.search-row:hover {
  cursor: pointer;
}

.search-row .search-row-name {
  max-width: 100%;
  font-size: 15px;
  font-weight: 400;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.search-row .search-row-value {
  width: 100%;
  height: 34px;
  display: flex;
  overflow-x: scroll;
}

.search-row .search-row-value::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.search-row .search-row-value .row-cell-value {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  flex-shrink: 0;
  text-align: center;
  margin: 0 8px;
}

.search-row .search-row-value .row-cell-value:first-child {
  justify-content: flex-start;
  margin: 0;
}

.search-row .search-row-value .collaborators-formmatter, 
.search-row .search-row-value .multiple-select-formmatter {
  width: 100%;
  overflow: hidden;
}

.search-row .search-row-value .multiple-select-formmatter {
  display: inline-flex;
}

.search-row .multiple-select-formmatter .grid_cell_type_multiple-select {
  margin-right: 10px;
}

.search-row .multiple-select-formmatter .grid_cell_type_multiple-select:last-child {
  margin-right: 0;
}

.search-row .search-row-value .cell-value-ellipsis {
  display: inline-block;
  height: auto;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.search-row .search-row-value .row-cell-value-empty {
  display: inline-block;
  height: 8px;
  width: 20px;
  background-color: #f1f1f1;
  border-radius: 4px;
}

.search-row .search-row-value .image-cell-value {
  height: 30px;
  width: 40px;
  position: relative;
  display: inline-flex;
  align-items: center;
}

.search-row .search-row-value .image-cell-value img {
  display: inline-block;
  height: 90%;
  width: 100%;
  border-radius: 3px;
}

.search-row-value .image-cell-value .cell-value-size {
  position: absolute;
  bottom: -2px;
  right: -4px;
  display: inline-block;
  height: 12px;
  padding: 0 4px;
  border-radius: 6px;
  text-align: center;
  background-color: #999;
  font-size: 10px;
  line-height: 12px;
  color: #fff;
} 

.search-row-value .number-formatter {
  width: 100%;
  height: 100%;
  display: inline-flex;
  align-items: center;
}

.search-row-value .number-formatter .column-name {
  padding-right: 4px;
  color: #999;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}