.deleted-rows-dialog {
  width: 1000px;
  max-width: 1000px;
  height: calc(100% - 56px);
}

.deleted-rows-dialog .modal-content {
  width: 100%;
  height: 100%;
}

.deleted-rows-dialog .modal-content .modal-body {
  overflow: hidden;
  padding: 0px;
}

.no-deleted-rows {
  height: 100%;
  width: 100%;
  text-align: center;
  color: #929292;
  padding: 16px;
}

.deleted-rows-container {
  height: 100%;
  width: 100%;
  font-size: 14px;
}

.deleted-rows-container .deleted-rows-header {
  height: 40px;
  width: calc(100% - 32px);
  margin: 0 16px;
  color: #999;
}

.deleted-rows-container .deleted-rows-header.deleted-row {
  padding-top: 6px;
}

.deleted-rows-container .deleted-rows-header.deleted-row .deleted-row-operator {
  padding-left: 50px;
}

.deleted-rows-container .deleted-rows-header.deleted-row:hover {
  cursor: unset;
  background-color: unset;
}

.deleted-rows-container .deleted-rows {
  height: calc(100% - 50px);
  width: 100%;
  padding: 0 16px 16px;
  overflow-y: scroll;
}

.deleted-rows-container .deleted-rows::-webkit-scrollbar {
  width: 0;
}

.deleted-rows-container .deleted-rows-content {
  height: auto;
  width: 100%;
}

.deleted-row {
  height: 50px;
  width: 100%;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e9ecef;
}

.deleted-row:hover {
  cursor: pointer;
  background-color: #f3f3f3;
}

.deleted-row .deleted-row-operator {
  width: 20%;
  height: 100%;
  display: inline-flex;
  align-items: center;
}

.deleted-row .operator-avatar-content {
  display: inline-block;
  height: 30px;
  width: 30px;
  margin: 0 10px;
}

.deleted-row .operator-avatar {
  display: inline-block;
  height: 30px;
  width: 30px;
  border-radius: 50%;
}

.deleted-row .operator-name {
  display: inline-block;
  width: calc(100% - 50px);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding-right: 20px;
}

.deleted-row .deleted-row-description {
  width: 55%;
  padding: 0 60px 0 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.deleted-row .deleted-row-date {
  width: 15%;
}

.deleted-row .deleted-row-restore {
  width: 10%;
  text-align: center;
  color: #F09F3F;
}