.operations-container {
  position: fixed;
  bottom: 0;
  right: 0;
  display: flex;
  width: 300px;
  height: calc(100% - 76px);
  flex-direction: column; 
  box-shadow: 0px 2px 10px 3px rgba(0, 0, 0, 0.2);
}

.operations-header {
  line-height: 42px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #dddddd;
  padding: 5px 1rem 0;
  background-color: #fff;
}

.operation-close {
  color: #999999;
  font-weight: bold;
  cursor: pointer;
}

.operation-close:hover {
  color: #555555;
}

.operation-wraper {
  flex: 1 1 auto;
  overflow-y: auto;
  overflow-x: hidden;  
  padding: 5px 0;
  background-color: #f1f1f1;
}

.operation-log-item {
  padding-left: 8px;
  padding-right: 8px;
}

.operation-log-item-title {
  height: 24px;
  line-height: 24px;
  font-size: 12px;
  width: 32px;
}

.operation-log-item-avartar {
  height: 24px;
  width: 24px;
  border-radius: 50%;
  margin-right: 8px;
}

.operation-operator {
  color: #F09F3F;
}

.operation-name {
  margin: 0 5px 0 5px;
}

.operation-table {
  flex: 1;
  color: #F09F3F;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.operation-log-item-time .time-content {
  flex: 1;
}

.btn-log-dropdownmenu {
  width: 22px;
  text-align: center;
  cursor: pointer;
}

.btn-log-dropdownmenu .dtable-icon-more-level {
  font-size: 14px;
}

.btn-log-dropdownmenu:hover .dtable-icon-more-level {
  color: #000;
}

.operation-log-item-body {
  width: calc(100% - 32px);
}

.operation-log-item-content {
  background-color: #ffffff;
  border-radius: 3px;
  padding: 4px;
  margin-bottom: 3px;
}

.operation-log-item-head {
  font-size: 14px;
  line-height: 24px;
}

.operation-log-item {
  font-size: 12px;
  color: #aaa;
  display: flex;
  flex-direction: row;
  margin-bottom: 5px;
}

.operation-log-item-content-title {
  font-size: 12px;
}

.operation-log-item-content-body {
  font-size: 14px;
  color: #000;
  word-wrap: break-word;
  word-break: break-all;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.operation-log-item-content-body .row-modified {
  width: 100%;
  padding: 2px 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.operation-log-item-content-body .grid_cell_type_single-select {
  display: inline-block;
  padding: 0px 10px;
  height: 20px;
  text-align: center; 
  border-radius: 10px; 
  font-size: 13px;
  max-width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.operation-log-item-content-body .left-to-right {
  padding: 0 4px;
  height: 20px;
  line-height: 20px;
}

.operation-log-item-content-body .grid_cell_type_multiple-select {
  margin: 3px 0;
}

.operation-image {
  max-height: 28px;
  margin-right: 2px;
  margin-bottom: 2px;
}

.operations-container .snapshots {
  overflow: auto;
  position: relative;
}

.operations-container .snapshots .snapshot-item {
  display: flex;
  justify-content: space-between;
  padding: 7px 16px;
  cursor: pointer;
  font-size: 14px; 

}
.operations-container .snapshots .snapshot-item:hover {
  background: #efefef;;
}

.operations-container .snapshots .snapshot-item:hover .snapshot-item-text {
  color: #F09F3F;
}