.statistic-dialog .statistic-modal {
  max-width: 1100px;
  width: 1100px;
  height: calc(100% - 56px);
}

.statistic-dialog .statistic-modal .modal-content {
  height: 100%;
}

.statistic-dialog .dtable-icon-return {
  color: #777777;
  font-size: 14px;
}

.statistic-body-container {
  padding: 0;
  overflow-y: hidden;
  height: calc(100% - 57px);
}

.statistic-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  height: 100%;
}

.statistic-table {
  width: 798px;
  overflow: hidden;
}

.statistic-table:hover {
  overflow: auto;
}

.statistic-table-container {
  width: 782px;
  padding: 25px 0;
  box-sizing: border-box;
  margin: 0 auto;
  height: calc(100% - 50px);
  overflow: auto;
}

.chart-container {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.statistic-container .statistic-table-wrapper {
  width: 700px;
  margin: 30px auto;
}

.statistic-table .statistic-table-title {
  display: flex;
  height: 20px;
  padding: 16px;
  align-items: center;
  height: 50px;
}

.statistic-table .statistic-table-title .statistic-table-title-name {
  font-weight: 500;
}

.statistic-table .statistic-table-title .dtable-font {
  height: 24px;
  width: 24px;
  margin-left: 10px;
  color: #999;
  cursor: pointer;
  display: flex;
  justify-content: center;
}

.statistic-table .statistic-table-title .dtable-font:hover {
  color: #000;
  opacity: 0.75;
}

.statistic-table .statistic-table-title .statistic-rename {
  width: 50%;
}

.statistic-table .statistic-table-title .statistic-rename:focus {
  box-shadow: 0 0 0 1px #2684ff;
  border: 1px solid #2684ff;
}

.statistic-table-container table {
  width: 100%;
  table-layout: fixed;
  position: relative;
}

.statistic-table-container table thead {
  border-top: 1px solid #ccc;
  font-size: 13px;
}

.statistic-table-container td, .statistic-table-container th {
  padding: 10px;
  border-right: 1px solid #ccc;
  border-left: 1px solid #ccc;
  overflow: hidden;
  min-width: 100px;
  width: 100px;
  font-weight: normal;
}

.statistic-table-container td>div, .statistic-table-container th>div {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 13px;
  max-width: 100%;
}

.statistic-table-container tr {
  border-bottom: 1px solid #ccc;
  text-align: center;
}

.statistic-container .statistic-parameter {
  width: 300px;
  background: #f8f8f8;
  height: 100%;
}

.statistic-parameter-container {
  width: 100%;
  position: relative;
  height: 100%;
}

.statistic-parameter-container .statistic-parameter-header {
  padding: 20px 0 0 0;
}

.stastics-mode-selector {
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #e4e4e4;
  cursor: pointer;
}

.stastics-mode-item {
  flex: 1 1 0;
  text-align: center;
  line-height: 36px;
  height: 36px;
}

.selected-stastics-mode-item {
  background-color: #f19645;
  color: #fff;
}

.statistic-parameter-container .statistic-parameter-body {
  padding: 0 20px;
  height: calc(100% - 36px);
  overflow: hidden;
  position: relative;
}

.statistic-parameter-container .statistic-parameter-body:hover {
  overflow: auto;
}

.statistic-container .statistic-parameter-list {
  width: 260px;
}

.statistic-container .statistic-parameter-title {
  padding-bottom: 5px;
  border-bottom: 1px solid #e4e4e4;
}

.statistic-divider {
  border-bottom: 1px solid #e4e4e4;
  margin-bottom: 14px;
}

.statistic-parameter-list .statistics-parameter-item {
  padding-bottom: 14px;
  line-height: 1;
}

.statistic-parameter-list .statistics-parameter-item label {
  font-size: 13px;
}

.parameter-icon {
  font-size: 12px;
}

.statistics-list-container {
  height: calc(100% - 40px);
  overflow: auto;
  display: flex;
  flex-wrap: wrap;
  padding: 15px 15px 25px 15px;
}

.statistic-item {
  width: 520px;
  height: 300px;
  border: 1px solid #ededed;
  margin-top: 15px;
  margin-right: 18px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  display: flex;
  flex-direction: column;
}

.statistic-item:nth-child(2n) {
  margin-right: 0; 
}

.chart-type-container {
  display: flex;
  height: 36px;
  flex-direction: row;
  background-color: #ffffff;
  border: 1px solid hsl(0,0%,80%);
  border-radius: 4px;
}

.chart-type-item {
  flex: 1 1 0%;
  text-align: center;
  line-height: 36px;
  color: #999999;
  cursor: pointer;
  position: relative;
}

.chart-type-item:hover:after {
  content: attr(tooltip_content);
  position: absolute;
  padding: 4px;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  display: inline-block;
  border-radius: 3px;
  line-height: 20px;
  text-align: center;
  left: 50%;
  bottom: -30px;
  word-break: keep-all;
  white-space: nowrap;
  z-index: 1050;
  transform: translateX(-50%);
}

.summarize-method-type-item:hover:after {
  display: none
}

.chart-type-item:first-child {
  border-radius: 4px 0 0 4px;
}

.chart-type-item:last-child {
  border-radius: 0 4px 4px 0;
}

.chart-type-item:only-child {
  border-radius: 4px;
}

.selected-chart-type-item {
  box-shadow: 0 0 0 1px rgb(229, 161, 82);
  color: rgb(229, 161, 82);
  background-color: rgba(229, 161, 82, .1);
}

.statistic-item-header {
  height: 50px;
  border-bottom: 1px solid #ededed;
  padding: 15px 20px;
  font-weight: 500;
  display: flex;
  justify-content: space-between;
}

.statistic-item-operations {
  display: inline-block;
  width: auto;
}

.statistic-item-operations .statistic-item-microscope {
  display: inline-block;
  width: 24px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  margin-right: 10px;
  color: #aaa;
}

.statistic-item-operations .statistic-item-microscope .dtable-icon-open {
  font-size: 14px;
}

.statistic-item-operations .statistic-item-microscope:hover {
  color: #000;
  cursor: pointer;
}

.statistic-item-table {
  overflow-y: hidden;
  flex: 1;
}

.statistic-item-table:hover {
  overflow-y: scroll;
}

.statistic-table-view {
  padding: 15px 20px;
  width: 510px;
}

.statistic-table-view .statistic-table-wrapper {
  overflow: hidden;
}

.statistic-table-view table {
  width: 470px;
  margin: 0 auto;
  position: relative;
  table-layout: fixed;
}

.statistic-table-view table thead {
  border-top: 1px solid #ccc;
  font-size: 13px;
}

.statistic-table-view td, .statistic-table-view th {
  padding: 5px;
  border-right: 1px solid #ccc;
  border-left: 1px solid #ccc;
  overflow: hidden;
  font-weight: normal;
}

.statistic-table-view td>div, .statistic-table-view th>div {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 13px;
  max-width: 100%;
}

.statistic-table-name {
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
  font-size: 14px;
}

.statistic-table-view tr {
  border-bottom: 1px solid #ccc;
  text-align: center;
}

.statistic-errmessage {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.statistic-toggle-parameter-container {
  margin-top: -11px;
  padding-bottom: 14px;
}

.statistic-toggle-parameter {
  height: 36px;
  padding-left: 8px;
  box-sizing: border-box;
  font-size: 14px;
  background-color: #fff;
  display: flex;
  border-radius: 4px;
  border: 1px solid hsl(0,0%,80%);
}

.statistic-toggle-parameter .custom-switch-indicator {
  width: 22px;
  height: 12px;
}

.statistic-toggle-parameter .custom-switch-indicator::before {
  height: 8px;
  width: 8px;
}

.statistic-toggle-parameter .custom-switch-input:checked ~ .custom-switch-indicator:before {
  left: 12px;
}

.chart-container .g2-guide-html {
  width: 100px;
  height: 80px;
  vertical-align: middle;
  text-align: center;
}

.chart-container .g2-guide-html .title {
  font-size: 12px;
  color: #8c8c8c;
  font-weight: 300;
  margin-bottom: 0.6rem;
}

.chart-container .g2-guide-html .value {
  font-size: 32px;
  color: #000;
  font-weight: bold;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.g2-tooltip-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.g2-tooltip-marker {
  display: inline-block;
  height: 5px;
  width: 5px;
  margin-right: 7px; 
  border-radius: 50%;
  line-height: 1;
}

.tooltip-value {
  margin-left: 4px;
}

.statistic-check-mark {
  height: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: scale(0.8)
}

.ststistic-colorinput {
  width: 18px;
  height: 18px;
}

.ststistic-colorinput.colorinput-color::before {
  display: none;
}

.pivot-table .column-title,
.pivot-table .row-title {
  padding: 0;
  text-align: center;
  color: inherit;
}

.pivot-table .column-title {
  caption-side: top;
}

.pivot-table .row-title {
  position: absolute;
  transform: rotate(-90deg);
  width: 100px;
  left: -60px;
  top: 50%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.pivot-table .pivot-cell {
  position: relative;
}

.pivot-table .pivot-cell:hover {
  cursor: pointer;
}

.pivot-table .selected-pivot-cell {
  border-bottom: 1px solid #66afe9;
  border-right: 1px solid #66afe9;
}

.pivot-table .selected-pivot-cell-top {
  border-bottom: 1px solid #66afe9;
}

.pivot-table .selected-pivot-cell-left {
  border-right: 1px solid #66afe9 ;
}

.pivot-table .selected-pivot-cell-border {
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  border: 1px solid #66afe9;
}

.pivot-table .pivot-empty-cell i {
  display: inline-block;
  height: 6px;
  width: 16px;
  border-radius: 6px;
  background: #f1f1f1;
}

.statistic-microscope-content {
  width: 100%;
  height: 100%;
  padding: 20px 0;
}

.statistic-microscope-content.microscope-table {
  padding: 0;
}

.statistic-microscope-content .statistic-table-container {
  height: 100%;
  width: calc(100% - 20px);
  padding: 0 30px;
}

.statistic-parameter-list .null-option-name {
  color: rgba(0, 0, 0, .25) !important;
}