.list-form-container {
  padding: 10 0;
  min-height: 20rem;
  max-height: 30rem;
  overflow: auto;
}

.list-form-item {
  display: flex;
  height: 3rem;
  line-height: 3rem;
  padding: 0 10px;
}

.list-form-item:hover {
  background-color: #f5f5f5;
}

.list-form-item .form-item-icon {
  font-size: 20px;
  color: #999;
}

.list-form-item .form-item-text {
  flex: 1;
  display: block;
  margin: 0 0.5rem;
  font-size: 14px;
  color: #000;
}
