.views-dropdown-menu {
  padding-bottom: 0 !important;
  border-radius: 4px;
}

.views-dropdown-menu .dropdown-list {
  padding-bottom: 0.5rem;
}

.views-dropdown-menu .view-item {
  display: flex;
  width: 24rem;
  height: 40px;
  padding: .25rem 15px;
  font-size: 14px;
  color: #555;
  font-weight: 500;
  align-items: center;
  justify-content: center;
}

.views-drawer-mobile .view-item-main,
.views-dropdown-menu .view-item-main {
  flex: 1;
  display: flex;
  align-items: center;
  height: 100%;
  overflow: hidden;
}

.views-dropdown-menu .dropdown-item:hover {
  background-color: #f5f5f5;
}

.views-dropdown-menu .dropdown-item.seleted-view {
  background-color: #e9e9e9;
}

.view-can-drop {
  background: #ececec;
}

.views-dropdown-menu .rdg-drag-view-handle {
  display: flex;
  align-items: center;
  margin-right: 6px;  
}

.views-dropdown-menu .rdg-drag-view-handle:hover {
  cursor: move;
  cursor: grab;
  cursor: -webkit-grab;
}

.views-dropdown-menu .rdg-drag-view-handle .dtable-font {
  color: #c2c2c2;
}

.views-dropdown-menu .view-content {
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.views-dropdown-menu .view-content:hover {
  cursor: pointer;
}

.views-dropdown-menu .view-content .dtable-font {
  margin-right: 6px;
  font-size: 14px;
}

.views-dropdown-menu .view-title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.views-dropdown-menu .more-view-operation {
  position: relative;
  padding: 0 5px;
}

.views-dropdown-menu .more-view-operation:hover {
  cursor: pointer;
}

.views-dropdown-menu .more-view-operation .dtable-font {
  color: #999;
}

.views-dropdown-menu .add-view-btn {
  padding: 0 15px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.views-drawer-mobile .views-list {
  background-color: #fff;
}

.views-drawer-mobile .views-list .view-item {
  display: flex;
  justify-content: space-between;
  margin-left: 0px !important;
  padding: 0.5rem 1rem;
  color: #555;
  border-bottom: 1px solid #e9e9e9;
}

.views-drawer-mobile .views-list .view-item:last-child {
  border-bottom: none;
}

.views-drawer-mobile .more-view-operation {
  margin: 0 0.5rem;
}
