.mobile-filters-view, .mobile-options-box {
  min-height: 100%;
  width: 100%;
  background-color: #F3F3F3;
  position: fixed;
  left: 0;
  top: 0;
}

.mobile-filters-view .mobile-filters-list {
  width: 100%;
  overflow-y: scroll;
}

.mobile-filters-view .mobile-filters-list::-webkit-scrollbar {
  width: 0;
}

.mobile-filters-view .mobile-empty-filters-list {
  padding: 16px;
  height: 150px;
  width: 100%;
}

.mobile-filters-list .am-list-body {
  border-bottom: 1px solid #e9e9e9 !important;
}

.mobile-filters-view .delete-filter {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid #e9e9e9;
  padding: 0 0 0 16px;
  color: #e94f4f;
  background: #fff;
  line-height: 50px;
}

.mobile-filters-view .filter-conjunction {
  width: 100%;
  height: 50px;
  margin-top: 16px;
  background: #fff;
  border-top: 1px solid #e9e9e9;
  border-bottom: 1px solid #e9e9e9 !important;
}

.mobile-filters-view .filter-conjunction .am-list-line::after {
  transform: scaleY(0) !important;
  background-color: #fff !important;
}

.am-action-sheet-button-list-item .current-option {
  display: block;
  height: 100%;
  width: 100%;
  background-color: #F3F3F3;
  padding: 0 16px;
}

.mobile-filterPredicate {
  padding: 0 16px;
}

.am-action-sheet-body .am-action-sheet-button-list .am-action-sheet-button-list-item {
  padding: 0;
}

.am-action-sheet-button-list-item {
  padding: 0;
}

.my-am-action {
  padding: 0 16px;
}

.filter-search-column {
  margin-top: 20px;
}

.am-list-content .form-control {
  width: 100%;
  border: none;
  border-radius: 0;
  padding-left: 0;
}

.am-list-content .form-control:focus {
  border: none;
  border-radius: 0;
}

.am-list-content .form-control[disabled] {
  height: 50px;
  padding-left: 6px;
}

.mobile-filters-view .am-list-item {
  padding-left: 16px;
  height: 50px;
}

.am-list-content .selector-collaborator {
  height: 100%;
  border: none;
  padding: 0;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.mobile-filters-view .selector-collaborator .collaborator, .mobile-option-collaborator .collaborator {
  height: 20px;
  line-height: 20px;
  margin-right: 10px;
  background: #ddd;
  padding: 0 8px 0 2px;
  border-radius: 10px;
  flex-shrink: 0;
}

.mobile-filters-view .selector-collaborator .collaborator-name,
.mobile-option-collaborator .collaborator .collaborator-name {
  margin-left: 5px;
}

.mobile-filters-view .collaborator-avatar-container img, .mobile-option-collaborator .collaborator img {
  position: relative;
  top: -1px;
  left: 0;
  height: 16px;
  width: 16px;
}

.mobile-filters-list .am-list-line .am-list-content {
  font-size: 14px;
}

.mobile-filter-dialog-input .am-list-line .am-list-content {
  padding: 0;
}

.am-list-content .multiple-option-name, .am-list-content .mobile-option-collaborator,
.am-list-content .mobile-single-option {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.am-list-content .multiple-option-name .dtable-font, .mobile-option-collaborator .dtable-font,
.mobile-single-option .dtable-font, .mobile-check-icon {
  font-size: 12px;
  color: #798d99;
}

.am-list-content .select-option-name {
  color: #202428;
  font-size: 14px;
}

.mobile-filter-not-input .am-list-line .am-list-content {
  padding: 6px 0;
}

.mobile-option-collaborator, .mobile-filters-list .am-list-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.mobile-filters-list .am-list-content .dtable-icon-right {
  color: #999;
}

.add-item-btn.mobile-add-btn {
  margin-top: 16px;
  background: #fff;
  color: #666;
  height: 50px;
  border-top: 1px solid #e9e9e9;
  border-bottom: 1px solid #e9e9e9;
  border-radius: 0;
  font-weight: 400;
}

.add-item-btn.mobile-add-btn:hover {
  background: #fff;
}

.my-list .am-list-body {
  border-top: 1px solid #e9e9e9 !important;
}

.mobile-filter-header {
  border-bottom: 1px solid #e9e9e9;
}

.mobile-am-list-header {
  background: #fff;
  padding: 0;
}

.empty-placeholder {
  color: #adb5bd;
}