.mobile-filter-dialog-input .am-list-line .am-list-content {
  padding: 0;
}

.my-list .am-list-body {
  border-top: 1px solid #e9e9e9 !important;
}

.am-list-content .form-control {
  width: 100%;
  border: none;
  border-radius: 0;
  padding-left: 0;
}

.am-list-content .form-control:focus {
  border: none;
  border-radius: 0;
}

.am-list-content .form-control[disabled] {
  height: 50px;
  padding-left: 6px;
}

.mobile-search-column {
  margin-top: 20px;
}

.mobile-search-column .form-control {
  height: 50px;
  box-sizing: border-box;
}

.mobile-search-column .form-control:focus {
  border-color: rgba(0, 40, 100, 0.12);
  box-shadow: none;
}

.mobile-options-list .search-column .form-control {
  border-left: none;
  border-right: none;
  border-radius: 0;
}

.mobile-filter-header {
  border-bottom: 1px solid #e9e9e9;
}

.mobile-options-box {
  min-height: 100%;
  width: 100%;
  background-color: #F3F3F3;
  position: fixed;
  left: 0;
  top: 0;
}

.mobile-am-list-header {
  background: #fff;
  padding: 0;
}

.mobile-list-header .am-list-header {
  height: 44px;
  padding: 16px 16px 5px;
}

.mobile-list {
  box-sizing: border-box;
}

.mobile-list .am-list-body {
  border-bottom: 1px solid #e9e9e9 !important;
  max-height: calc(100% - 44px);
  overflow-y: scroll;
}

.mobile-list .am-list-body::-webkit-scrollbar {
  width: 0;
}

.empty-placeholder.none-search-result {
  padding: 13px 16px;
}

.am-list-body div:not(:last-child) .am-list-line::after {
  transform: scaleY(1) !important;
  background-color: #e9e9e9 !important;
}