.row-expand-dialog {
  height: calc(100% - 56px);
}

.row-expand-dialog .modal-header {
  padding: 6px 14px 6px 20px;
}

.header-close-list {
  display: flex;
}

.row-expand-comments .modal-header {
  padding: 6px 14px;
  position: relative;
  display: flex;
  justify-content: flex-end;
}

.comment-close-icon {
  height: 24px;
  width: 24px;
  text-align: center;
  cursor: pointer;
  color: #000;
  opacity: 0.5;
  font-weight: 700;
}

.comment-close-icon:hover {
  opacity: 0.75;
}

.row-expand-content-container {
  display: flex;
  flex-direction: row;
}

.row-expand-comments {
  width: 300px;
  background: #f3f3f3;
  display: flex;
  flex-direction: column;
  border-radius: 0 3px 3px 0;
}

.row-expand-details {
  width: 720px;
  display: flex;
  flex-direction: column;
}

.row-expand-comments .row-comment-header .modal-title {
  width: calc(100% - 28px);
  position: absolute;
}

.row-expand-comments .row-comment-header .row-comment-operation {
  color: #666;
  font-size: 14px;
  font-weight: 400;
  padding: 8px 0px;
}

.row-expand-comments .row-comment-header .row-comment-operation:hover {
  cursor: pointer;
}

.row-expand-comments .row-comment-header .row-comment-operation:last-child {
  margin-left: 16px;
}

.row-expand-comments .row-comment-header .row-comment,
.row-expand-comments .row-comment-header .row-operation {
  color: #212529;
  border-bottom: 2px solid #eb8205;
}

.row-expand-comments .row-comment-body {
  padding: 0;
  height: calc(100% - 40px);
}

.details-header-url-btn {
  display: inline-block;
  height: 24px;
}

.row-expand-dialog .modal-content {
  height: 100%;
}

.expand-row-container {
  padding: 20px;
  overflow-y: scroll;
}

.expand-row-container .row-comment-count {
  position: absolute;
  right: 16px;
  top: 0;
  font-size: 14px;
  color: #666666;
  cursor: pointer;
}

.expand-row-container .row-expand-item {
  margin-bottom: 30px;
}

.expand-row-container .row-expand-item label {
  font-size: 14px;
  color: #202428;
  margin-bottom: 10px;
}

@media screen and (max-width: 991.8px) {
  .expand-row-container .row-expand-item label {
    font-weight: bold;
  }
}

@media screen and (min-width: 991.8px) {
  .expand-row-container .row-expand-item label {
    font-weight: 500;
  }
}

.expand-row-container .row-expand-item label.simple-text, 
.expand-row-container .row-expand-item label.multiple-select, 
.expand-row-container .row-expand-item label.single-select,
.expand-row-container .row-expand-item label.collaborator {
  margin-bottom: 5px;
}

.row-expand-img-content {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.row-expand-container-img,
.row-expand-container-file {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.row-expand-view .row-expand-container-img,
.row-expand-view .row-expand-container-file {
  justify-content: flex-start;
  margin-left: -10px;
  margin-right: -10px;
  margin-bottom: -20px;
}

.row-expand-item-img {
  height: 210px;
  width: 210px;
  border: 2px solid #ededed;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 20px;
  margin-bottom: 20px;
  position: relative;
}

.row-expand-item-img:first-child {
  margin-left: 0;
  margin-top: 0;
}

.row-expand-item-img:nth-child(2), .row-expand-item-img:nth-child(3) {
  margin-top: 0;
}

.row-expand-item-img:nth-child(4n) {
  margin-left: 0;
}

.row-expand-item-file {
  border: 2px solid #ededed;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 95px;
  width: 95px;
  margin: 10px 0 10px 20px;
}

.row-expand-item-img:hover, .row-expand-item-file:hover {
  border: 2px solid #c9c9c9;
}

.row-expand-item-img img {
  display: block;
  max-width: 206px;
  max-height: 206px;
  border-radius: 4px;
}

.row-expand-item-file img {
  display: block;
  width: 91px;
  height: 91px;
  border-radius: 4px;
}

.row-expand-dialog .row-expand-btn {
  min-width: 80px;
  max-width: 100%;
  height: 28px;
  padding: 0 10px;
  background-color: #f0f0f0;
  border-radius: 3px;
  color: #929292;
  font-size: 14px;
  text-align: center;
  display: table-cell;
  vertical-align: middle;
}

.row-expand-dialog .row-expand-btn:hover {
  background-color: #dbdbdb;
  color: #666666;
  cursor: pointer;
}

.row-expand-dialog .option-item-container {
  height: 30px;
  display: block;
  cursor: pointer;
  padding: 5px;
  margin-bottom: 0;
}

.row-expand-dialog .options-list {
  min-height: 30px;
  cursor: pointer;
  padding: 0 5px;
  margin-bottom: 0;
  line-height: 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.row-expand-dialog .option-item-container:hover,
.row-expand-dialog .options-list:hover {
  background-color: #f5f5f5;
  border-radius: 2px;
}

.row-expand-dialog .long-text-container {
  background-color: #f5f5f5;
  border-radius: 2px;
  cursor: pointer;
  padding: 5px;
}

.row-expand-dialog .long-text-container ol,
.row-expand-dialog .long-text-container ul {
  padding-inline-start: 40px;
  margin-bottom: 1em;
}

.row-expand-dialog .options-list .multiple-select-item {
  width: auto;
  height: 20px;
  margin: 5px 0;
  box-sizing: border-box;
  flex-shrink: 0;
}

.row-expand-dialog .option-item-container .option-item-name,
.row-expand-dialog .options-list .option-item-name {
  font-size: 13px;
  line-height: 20px;
  width: fit-content;
  padding: 0 10px;
  border-radius: 10px;
  cursor: pointer;
}

.row-expand-dialog .options-list .multiple-select-option {
  display: inline-flex;
  height: 20px;
  font-size: 13px;
}

.row-expand-dialog .options-list .option-item-name-container {
  max-width: 120px; 
  overflow: hidden; 
  text-overflow: ellipsis;
}

.row-expand-dialog .options-list .remove-container {
  width: 16px;
  margin-left: 5px;
  text-align: right;
}

.row-expand-dialog .options-list .remove-icon {
  display: inline-block;
  position: relative;
  right: -2px;
  width: 14px;
  height: 20px;
  cursor: pointer;
}

.multiple-select-item .multiple-select-option {
  max-width: 150px;
  margin: 0 10px 0 0;
}

.multiple-select-option .remove-container .dtable-font {
  display: inline-block;
  font-size: 12px;
  color: #909090;
  transform: scale(.8);
}

.row-expand-dialog .expand-row-icon .dtable-font {
  color: #999;
}

.row-expand-dialog .modal-header-btn {
  height: 24px;
  min-width: 24px;
  padding: 0px;
  border: none;
  margin-left: 2px;
  color: #929292;
}

.row-expand-dialog .modal-header-btn:hover {
  color: #212529;
}

.row-expand-container-collaborator {
  position: relative;
}

.row-expand-collaborators {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.row-expand-collaborators .collaborator {
  display: inline-flex;
  height: 20px;
  margin-right: 10px;
  padding: 0 8px 0 2px;
  font-size: 13px;
  border-radius: 10px;
  background: #eaeaea;
}

.row-expand-collaborators .collaborator-avatar-container {
  width: 16px;
}

.row-expand-collaborators .collaborator-avatar {
  width: 16px;
  height: 16px;
  transform: translateY(-1px);
}

.row-expand-collaborators .collaborator-name {
  margin-left: 5px;
}

.row-expand-collaborators .remove-container {
  width: 16px;
  text-align: right;
}

.row-expand-collaborators .remove-icon {
  display: inline-block;
  position: relative;
  right: -2px;
  width: 14px;
  height: 20px;
  cursor: pointer;
}

.row-expand-collaborators .remove-icon .dtable-font {
  display: inline-block;
  font-size: 12px;
  color: #909090;
  transform: scale(.8);
}

.row-expand-item-img .image-item-info {
  display: none;
  height: 30px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
  padding: 0 8px;
  justify-content: space-between;
  align-items: center;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

.row-expand-item-img:hover .image-item-info {
  display: flex;
}

.row-expand-item-img .image-item-info .image-item-name {
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #fff;
}

.row-expand-item-img .image-item-info  .image-item-icon {
  display: inline-block;
  width: auto;
}

.row-expand-item-img .image-item-info .image-item-operation {
  display: inline-block;
  height: 20px;
  width: 20px;
  text-align: center;
  line-height: 20px;
  color: #dbdbdb;
}

.row-expand-item-img .image-item-operation .dtable-font {
  font-size: 14px;
}

.row-expand-item-img .image-item-info .image-item-operation:hover {
  color: #fff;
}

.row-expand-comments .row-operation-body {
  padding: 10px;
  height: calc(100% - 40px);
  overflow-y: scroll; 
}

.row-expand-comments .row-operation-body::-webkit-scrollbar {
  width: 0;
}

.row-operation-body .row-opearion-list {
  width: 100%;
  height: auto;
}

.row-operation-body .operation-log-item-body {
  height: auto;
  width: calc(100% - 32px);
  display: flex;
  flex-direction: column;
}

.row-operation-body .operation-log-item-head {
  height: 24px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.row-operation-body .row-opearion-item-op-time {
  font-size: 10px;
  color: #777;
}

.row-operation-body .operation-log-item-content .row-opearion-item-column-icon {
  font-size: 12px;
  margin-right: 6px;
}

.row-operation-body .operation-log-item-content-title {
  margin-bottom: 6px;
  font-size: 12px;
  color: #aaa;
}

.row-opearion-image-container .image-formatter img,
.row-opearion-image-container .file-formatter img {
  margin-top: 2px;
  margin-bottom: 2px;
}

.row-opearion-list .collaborators-formmatter .formmatter-show,
.row-opearion-list .links-formmatter .formmatter-show {
  flex-wrap: wrap;
  min-height: auto;
}

.row-opearion-list .collaborators-formmatter .collaborator,
.row-opearion-list .links-formmatter .link,
.row-opearion-list .operation-log-item-content .grid_cell_type_multiple-select {
  margin-top: 2px;
  margin-bottom: 2px;
  margin-right: 4px;
}

.row-opearion-list .collaborators-formmatter .collaborator:last-child,
.row-opearion-list .links-formmatter .link:last-child,
.row-opearion-list .operation-log-item-content .grid_cell_type_multiple-select:last-child {
  margin-bottom: 0;
}

.row-opearion-list .links-formmatter .link-name {
  max-width: 200px;
}

.row-opearion-list .checkbox {
  margin-left: 0;
  transform: unset;
}

.row-opearion-list .operation-log-item {
  margin-bottom: 14px;
}

.row-opearion-list .operation-log-item:last-child {
  margin-bottom: 0;
}

.row-opearion-list .operation-log-item-title {
  transform: translateY(6px);
}

.row-opearion-list .operation-operator {
  display: inline-block;
  max-width: 124px;
  font-size: 14px;
  color: #333;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.row-opearion-list .longtext-formatter {
  max-width: 100%;
}

.row-opearion-list .left-to-right {
  padding: 0 6px;
}

.row-opearion-list .array-left-to-right {
  padding: 0 6px 0 0;
}

.row-opearion-list .operation-log-item-content {
  padding: 6px;
}

.row-opearion-list .collaborators-formmatter .formmatter-show {
  align-items: unset;
}