.file-chooser-modal {
  width: 800px;
}

.file-chooser-content-container {
  width: 100%;
  display: flex;
  min-height: 350px;
}

.file-chooser-content-container .file-chooser-left .file-chooser-nav {
  padding-right: 10px;
}

.file-chooser-left {
  width: 162px;
  padding: 16px 0 10px 16px;
  border-right: 1px solid #e9ecef;
}

.file-chooser-content-container .file-chooser-left .file-chooser-nav .file-chooser-nav-item {
  padding: 5px 0 5px 12px;
  cursor: pointer;
  border-radius: 3px;
}

.file-chooser-content-container .file-chooser-left .file-chooser-nav .file-chooser-nav-item:hover {
  background-color: #feefb8;
}

.file-chooser-content-container .file-chooser-left .select-file-chooser-nav-item {
  background-color: #f3af7d !important;
  color: #fff;
}

.file-chooser-content-container .file-chooser-right {
  width: 636px;
}

.file-chooser-container {
  height: 365px;
  padding: 10px;
  overflow: auto;
  border-right: 1px solid #eee;
}

.file-chooser-container .file-chooser-header {
  position: relative;
  padding-left: 24px;
}

.file-chooser-container .file-chooser-header .file-choose-icon {
  color: #9aa0ac;
  position: absolute;
  left: 0;
  top: 0;
  text-align: center;
  width: 24px;
  height: 24px;
  line-height: 24px;
  font-size: 12px;
  transform: scale(0.8);
}

.file-chooser-content {
  position: relative;
  padding-left: 22px;
}

.file-chooser-item .item-info {
  height: 1.5rem;
  cursor: pointer;
  position: relative;
  line-height: 1.625;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}

.file-chooser-item .item-info .dtable-font {
  color: #9aa0ac;
  transform: scale(0.8);
}

.file-chooser-item .item-info .item-text {
  padding-left: 2.8rem;
  font-size: 15px;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  line-height: 24px;
}

.file-chooser-item .item-info .item-left-icon {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  top: 0;
  left: 0;
  padding-left: 1.5rem;
  color: #ccc;
  font-size: 14px;
}

.item-toggle {
  position: absolute;
  height: 1.5rem;
  width: 1.5rem;
  left: 0;
  top: 0;
  line-height: 1.5rem !important;
  text-align: center;
  cursor: pointer;
  color: #9aa0ac;
  font-size: 12px!important;
}

.file-chooser-item {
  position: relative;
  padding-left: 22px;
}

.file-chooser-item .item-active {
  background: #F3AF7D !important;
  border-radius: 2px;
  -webkit-box-shadow: inset 0 0 1px #999;
  box-shadow: inset 0 0 1px #999;
  color: #fff;
}

.file-chooser-item .item-active .dtable-font {
  color: #fff;
}

.file-chooser-item .item-info:hover {
  background: #FDEFB9;
  border-radius: 2px;
  -webkit-box-shadow: inset 0 0 1px #999;
  box-shadow: inset 0 0 1px #999;
}

.repo-chooser-info:hover {
  background: #FDEFB9;
  border-radius: 2px;
  -webkit-box-shadow: inset 0 0 1px #999;
  box-shadow: inset 0 0 1px #999;
}

.upload-image-container {
  max-width: 100%;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 365px;
  width: 354px;
}

.recent-added-item {
  position: relative;
}

.recent-added-item .item-info {
  height: 1.5rem;
  cursor: pointer;
  position: relative;
  line-height: 1.625;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}

.recent-added-item .item-info:hover {
  background: #FDEFB9;
  border-radius: 2px;
  -webkit-box-shadow: inset 0 0 1px #999;
  box-shadow: inset 0 0 1px #999;
}

.recent-added-item .item-info .dtable-font {
  color: #9aa0ac;
  transform: scale(0.8);
}

.recent-added-item .item-info .item-text {
  padding-left: 2.8rem;
  font-size: 15px;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  line-height: 24px;
}

.recent-added-item .item-info .item-left-icon {
  position: absolute;
  display: flex;
  align-items: center;
  top: 0;
  left: 0;
  padding-left: 1.5rem;
  color: #ccc;
  font-size: 14px;
}

.recent-added-item .item-active {
  background: #F3AF7D !important;
  border-radius: 2px;
  -webkit-box-shadow: inset 0 0 1px #999;
  box-shadow: inset 0 0 1px #999;
  color: #fff;
}

.recent-added-item .item-active .dtable-font {
  color: #fff;
}