/* tables tabs */
.tables-tabs-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #F09F3F;
}

.tables-tabs-container .tables-tabs-content {
  display: flex;
  align-items: center;
  margin-left: 24px;
  overflow: hidden;
}

.tables-tabs-content .tables-nav {
  position: relative;
  display: flex;
  padding-right: 14px;
}

.tables-tabs-content .tables-tabs {
  position: relative;
  display: flex;
  flex: 1 1 0;
  padding-right: 10px;
  overflow: hidden;
}

.tables-tabs-content .tabs-nav-scroll-before {
  position: absolute;
  top: 0;
  left: 0;
  width: 25px;
  height: 34px;
  background: linear-gradient(to left, rgba(126, 116, 105, 0), #F09F3F);
  z-index: 5;
}


.tables-tabs-content .tabs-nav-scroll-after {
  position: absolute;
  width: 20px;
  height: 34px;
  top: 0;
  right: 10px;
  background: linear-gradient(to right, rgba(240, 159, 63, 0), #F09F3F);
}


.tables-tabs-content .tabs-nav-scroll {
  position: relative;
  overflow-x: scroll;
  overflow-y: hidden;
  scrollbar-width: none;
}

.tables-tabs-content .tables-nav-tabs {
  display: inline-flex;
  margin: 0;
  padding-left: 0;
}

.tables-tabs-content .tabs-nav-scroll::-webkit-scrollbar {
  display: none;
  width: 0;
  height: 0;
}

.tables-tabs-content .tabs-tab {
  display: flex;
  flex: 1 0 auto;
  padding: 0 6px 0 0;
  transition: .3s color;
  align-items: center;
  list-style: none;
}

.tables-tabs-content .tab-link {
  display: inline-block;
  height: 26px;
  padding: 0 16px;
  line-height: 26px;
  text-align: center;
  color: #fff;
  background-color: #e08e2e;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  border: 0;
}

.tables-tabs-content .tab-link.tab-can-drop {
  background: #fff;
  color: #000;
}

.tables-tabs-content .tab-link:hover {
  color: #ffe3c0;
  background-color: #d4872d;
  cursor: pointer;
}

.table-tabs-container .nav-link.active {
  color: #000;
  background-color: #fff;
}

.tables-tabs-content .tabs-tab.tabs-tab-avtive .tab-link {
  color: #000;
  background-color: #fff;
  cursor: default;
}

.tables-tabs-content .tabs-tab.tabs-tab-avtive .tab-link-dragging:hover {
  cursor: move;
  cursor: grab;
  cursor: -webkit-grab;
}

.tables-tabs-content .tab-title {
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
}

.tables-tabs-content .tab-dropdown {
  display: inline-block;
  margin-left: 5px;
  padding: 0 5px;
  cursor: pointer;
}

.tables-tabs-content .dtable-icon-drop-down {
  display: inline-block;
  margin: 0;
  font-size: 12px;
  color: #949494;
  transform: scale(.8);
  transition: all .1s;
}

.tables-tabs-content .tab-dropdown:hover .dtable-icon-drop-down {
  color: #aaa;
}

.tables-tabs-content .tables-scroll-control {
  width: 50px;
  margin-right: 10px;
}

.tables-tabs-content .tables-scroll-control .scroll-control-btn {
  display: inline-block;
  width: 20px;
  height: 20px;
  text-align: center;
  border-radius: 2px;
}

.tables-tabs-content .scroll-active {
  background-color: #e08e2e;
} 

.tables-tabs-content .scroll-active:hover {
  cursor: pointer;
  background-color: #d4872d;
}

.tables-tabs-content .scroll-next {
  margin-left: 6px;
}

.tables-tabs-content .scroll-control-btn .dtable-font {
  display: inline-block;
  font-size: 12px;
  color: #fff;
  transform: scale(.8) translateY(-3px);
}

.tables-tabs-content .add-table {
  display: flex;
  align-items: center;
  width: 30px;
  height: 26px;
  margin-right: 30px;
}

.tables-tabs-content .add-table-button {
  width: 14px;
  height: 14px;
  line-height: 10px;
  border-radius: 2px;
  background-color: #fff;
  text-align: center;
}

.tables-tabs-content .add-table-button:hover {
  color: #F09F3F;
  background-color: #FFEEDA;
  cursor: pointer;
}

.tables-tabs-content .dtable-icon-add-table {
  display: inline-block;
  font-size: 10px;
  font-weight: bold;
  color: #F09F3F;
  transform: scale(.8);
  vertical-align: -1px;
}

.tables-tabs-container .tables-tabs-operations {
  display: flex;
  align-items: center;
}

.tables-tabs-operations .tables-share,
.tables-tabs-operations .tables-logs,
.tables-tabs-operations .tables-plugins {
  display: flex;
  margin-right: 8px;
  cursor: pointer;
}

.tables-tabs-operations .tables-share .tables-share-icon,
.tables-tabs-operations .tables-logs .tables-logs-icon,
.tables-tabs-operations .tables-plugins .tables-plugins-icon {
  display: flex;
  justify-content: center;
  width: 24px;
  height: 24px;
  color: #fff;
}

.tables-tabs-operations .tables-plugins .tables-plugins-comment {
  display: flex;
  align-items: center;
  min-width: 40px;
  margin-left: 4px;
  font-size: 13px;
  color: #fff;
  white-space: nowrap;
}

.table-toolbar {
  width: 100%;
  min-height: 48px;
  background-color: #fff;
  padding: 8px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
}

.table-toolbar .toolbar-item {
  position: relative;
  display: inline-block;
  font-size: 14px;
  color: #555;
  font-weight: 500;
}

.table-toolbar .toolbar-item.plugin-toolbar-item {
  height: 22.5px;
  line-height: 22.5px;
  vertical-align: -3px;
}

.table-toolbar .toolbar-item .dtable-font {
  font-size: 14px;
  transform: translateY(1px);
}

.table-toolbar .view-item {
  padding-left: 0;
  margin-left: 1rem !important;
}

.table-toolbar .view-item:hover {
  cursor: pointer;
}

.table-toolbar .view-item .dtable-icon-drop-down {
  display: inline-block;
  font-size: 12px;
  color: #949494;
  transform: scale(.8);
  transition: all .1s;
}

.table-toolbar .view-item .dtable-icon-drop-down:hover {
  color: #aaa;
}

.table-toolbar .toolbar:last-child {
  padding-right: 0;
}

.toolbar-btn {
  display: inline-flex;
  height: 22px;
  padding: 0 .5rem;
  border-radius: 3px;
  align-items: center;
  transition: all .1s ease-in;
}

.toolbar-btn:hover {
  background-color: #f1f1f1;
  cursor: pointer;
}

.toolbar-btn.view-btn:hover {
  background: none;
}

.toolbar-btn .dtable-font {
  margin-right: 6px;
  font-size: 14px;
  transform: translateY(1px);
}

.toolbar-btn.hidden:hover,
.toolbar-btn.grouped:hover {
  box-shadow: inset 0 0 0 2px rgba(0,0,0,0.1);
}

.toolbar-btn .dtable-icon-filter,
.toolbar-btn .dtable-icon-main-view ,
.toolbar-btn .dtable-icon-sort {
  font-size: 14px;
  transform: translateY(1px);
}

.toolbar-btn.toolbar-actived-filter {
  background-color: #d1f7c4;
}

.toolbar-btn.toolbar-actived-sort {
  background-color: #f5d9bc;
}

.toolbar-btn.toolbar-actived-sort:hover,
.toolbar-btn.toolbar-actived-filter:hover {
  box-shadow: inset 0 0 0 2px rgba(0,0,0,0.1);
}

.view-lock .lock-view-btn .dtable-font {
  margin-right: 0;
}

.view-lock .lock-view-btn.lock {
  background-color: rgb(245, 218, 214);
}

.view-lock .lock-view-btn.lock:hover {
  box-shadow: inset 0 0 0 2px rgba(0,0,0,0.1);
}

.view-hide .hide-columns-title.hidden {
  background-color: #d7e8ff;
}

.view-hide .hide-columns-title.hidden:hover {
  box-shadow: inset 0 0 0 2px rgba(0,0,0,0.1);
}

.view-group .group-title.grouped {
  background-color: #dad7ff;
}

.view-menu-btn .toolbar-btn .dtable-icon-more-level {
  margin-right: 0;
}

.table-toolbar-right {
  box-sizing: border-box;
  margin-right: 10px;
}

.search-input {
  height: 32px;
}

.table-toolbar-right .calendar-view-btn {
  display: inline-block;
  color: #666;
  padding: 0 4px;  
  margin-right: 6px;
  cursor: pointer;
}

.table-toolbar-right .calendar-view-btn:hover {
  background-color: #F3F3F3;
  border-radius: 3px;
}

/* 
  use this selector to cover seafile-ui style
*/
.input-icon .search-input:not(:first-child) {
  padding-left: 30px;
}

/* 
  use this selector to cover seafile-ui style
*/
.input-icon .search-input:not(:last-child) {
  padding-right: 60px;
}

.search-container {
  position: relative;
}

.search-container .Search-all-Tables {
  height: 32px;
  width: 100%;
  position: absolute;
  top: 36px;
  left: 0;
  z-index: 3;
  border: 1px solid rgba(0, 40, 100, 0.12);
  background-color: #fff;
  line-height: 30px;
  color: #666;
  padding: 0 10px;
  box-shadow: 0 0 5px #ccc;
  font-size: 14px;
}

.search-container .Search-all-Tables:hover {
  cursor: pointer;
}

.search-poll-button {
  display: flex;
  right: 25px;
  line-height: 32px;
  left: auto;
  text-align: center;
  font-size: 12px;
  min-width: 35px;
  pointer-events: all;
}

.search-poll-button .dtable-font {
  font-size: 12px;
  cursor: pointer;
}

.search-icon {
  text-align: center;
  line-height: 32px;
  min-width: 2.0rem;
}

.search-exchange-btn {
  width: 20px;
  line-height: 1.8;
  height: 20px;
  background-color: #efefef;
  color: #333;
  display: block;
}

.search-upward {
  border-radius: 2px 0 0 2px;
  transform: scale(0.8, 0.8) translateX(6px);
}

.search-backward {
  border-radius: 0 2px 2px 0;
  transform: scale(0.8, 0.8);
}

.search-exchange-btn:hover {
  background-color: #d8d8d8;
}

.search-text-clear {
  cursor: pointer;
  min-width: 25px;
  pointer-events: all;
  font-style:normal;
  font-size: 18px;
  font-weight: 700;
}

.toolbar-btn .active-search {
  display: inline-block;
  height: 32px;
  line-height: 32px;
  color: #666666;
}

.am-action-sheet-button-list {
  text-align: left;
  color: #333;
}

.am-action-sheet-button-list .am-action-sheet-button-list-item .dtable-font {
  color: #888;
  font-size: 20px;
  margin-right: 16px;
}

.am-action-sheet-button-list .am-action-sheet-button-list-item {
  padding: 0 16px;
  height: 56px;
  line-height: 56px;
  color: #333;
  font-size: 16px;
  overflow-y: hidden;
}

html:not([data-scale]) .am-action-sheet-button-list .am-action-sheet-button-list-item::before {
  background-color: #fff;
}

.table-toolbar-right-mobile {
  width: 100%;
}

.table-toolbar-right-mobile .search-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.table-toolbar-right-mobile .search-container .input-icon {
  width: calc(100% - 60px);
  margin-left: 10px;
}

.table-toolbar-right-mobile .search-container .search-button-cancel {
  max-width: 50px;
}

.table-toolbar-right-mobile .form-control {
  background-color: #f5f5f5;
}

.table-toolbar-right-mobile .form-control:focus {
  border-color: rgba(0, 40, 100, 0.12);
  box-shadow: none;
}

@media screen and (max-width: 991.8px) {
  .toolbar-btn .active-search {
    padding: 0 10px;
  }

  .toolbar-btn.view-btn span {
    max-width: 90px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .tables-tabs-content .add-table {
    margin-right: 0px;
  }
}
