.collaborators-editor-list {
  width: 300px;
  min-height: 160px;
}

.collaborators-editor-list .collaborators-container {
  max-height: 200px;
  padding: 10px;
  overflow: auto;
}

.collaborators-editor-list .search-collaborators {
  padding: 0 10px;
}

.collaborators-editor-list .search-collaborators input {
  height: 28px;
}

.collaborators-editor-list .collaborator-item {
  display: flex;
  align-items: center;
  padding-left: 0;
  padding-right: 0;
}

.collaborators-editor-list .collaborator-item:hover {
  background: none;
}

.collaborators-editor-list .collaborator-container {
  display: flex;
  align-items: center;
  width: 100%;
  height: 30px;
  border-radius: 2px;
  font-size: 14px;
}

.collaborators-editor-list .collaborator-container:hover {
  background: #f5f5f5;
  cursor: pointer;
}

.collaborators-editor-list .collaborator {
  flex: 1;
}

.collaborator-avatar {
  border-radius: 50%;
  vertical-align: middle;
}

.collaborators-editor-list .collaborator-avatar {
  width: 16px;
  height: 16px;
  line-height: 16px;
  margin-left: 10px;
  transform: translateY(-1px);
}

.collaborators-editor-list .collaborator-name {
  margin-left: 4px;
}

.collaborators-editor-list .collaborator-item:active .collaborator-name {
  color: #212529;
}

.collaborators-editor-list .collaborator-check-icon {
  width: 20px;
  text-align: center;
}

.collaborators-editor-list .collaborator-check-icon .dtable-font {
  font-size: 12px;
  color: #798d99;
}

.collaborators-editor-list .none-search-result {
  font-size: 14px;
  opacity: .5;
  display: inline-block;
}

.collaborators-formmatter {
  display: inline-flex;
  align-items: center;
  overflow: hidden;
  flex-wrap: nowrap;
}

.collaborators-formmatter .formmatter-show {
  display: inline-flex;
  align-items: center;
  min-height: 28px;
}

.editor-list-delete .collaborator,
.collaborators-formmatter .collaborator {
  display: inline-flex;
  height: 20px;
  margin-right: 10px;
  padding: 0 8px 0 2px;
  font-size: 13px;
  border-radius: 10px;
  background: #eaeaea;
}

.collaborators-formmatter .collaborator-avatar-container {
  width: 16px;
}

.collaborators-formmatter .collaborator-avatar {
  width: 16px;
  height: 16px;
  transform: translateY(-1px);
}

.collaborators-formmatter .collaborator-name {
  margin-left: 5px;
}

.collaborators-formmatter .remove-container {
  width: 16px;
  text-align: right;
}

.collaborators-formmatter .remove-icon {
  display: inline-block;
  position: relative;
  right: -2px;
  width: 14px;
  height: 20px;
  cursor: pointer;
}

.collaborators-formmatter .remove-icon .dtable-font {
  display: inline-block;
  font-size: 12px;
  color: #909090;
  transform: scale(.8)
}

.editor-list-delete {
  background-color: #f6f6f6;
  padding: 5px;
  border-bottom: 1px solid #dde2ea;
  border-radius: 3px 3px 0 0;
}

.editor-list-delete .remove-container {
  width: 16px;
  text-align: right;
}

.editor-list-delete .remove-container:hover {
  cursor: pointer;
}

.editor-list-delete .remove-icon {
  display: inline-block;
  position: relative;
  right: -2px;
  width: 14px;
  height: 20px;
}

.editor-list-delete .remove-icon .dtable-font {
  display: inline-block;
  font-size: 12px;
  color: #909090;
  transform: scale(.8);
}
