.search-tables-dialog {
  width: 800px;
  max-width: 800px;
  height: calc(100% - 56px);
}

.search-tables-dialog .modal-content {
  width: 100%;
  height: 100%;
}

.search-tables-dialog .search-container {
  height: 100px;
  padding: 0 20px;
  background-color: #f8f8f8;
}

.search-tables-dialog .search-container .modal-title {
  width: 100%;
}

.search-tables-dialog .search-container .search-input-container {
  margin-top: 17px;
  height: 40px;
  position: relative;
}

.search-input-container .search-tables-input {
  width: 100%;
  height: 38px;
  line-height: 46px;
  border: unset;
  border-bottom: 2px solid #ededed;
  border-radius: unset;
  padding: 0 40px 0px 38px;
}

.search-input-container .search-tables-input:focus {
  outline: none;
  box-shadow: unset;
}

.search-input-container .search-icon {
  position: absolute;
  left: 0px;
  margin-left: 3px;
  line-height: 38px;
  text-align: center;
  color: #aaa;
}

.search-container .close{
  height: 24px !important;
  width: 24px;
  line-height: 24px;
  position: absolute;
  right: 14px;
  top: 22px;
  padding: 0;
  margin: 0;
}

.search-input-container .clear-search-text {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 24px;
  top: 6px;
  height: 24px;
  width: 24px;
  border-radius: 50%;
  text-align: center;
  line-height: 24px;
  color: #ccc;
}

.search-input-container .clear-search-text .dtable-font {
  font-size: 12px;
}

.search-input-container .clear-search-text:hover {
  cursor: pointer;
}

.search-container .search-result-abstract {
  height: 42px;
  width: 100%;
  margin-top: -1px;
  color: #aaa;
  font-size: 14px;
  padding-left: 16px;
  line-height: 42px;
  font-weight: 400;
}

.search-tables-container {
  width: 100%;
  height: calc(100% - 100px);
  padding: 10px 20px;
  overflow-y: scroll;
  background-color: #f8f8f8;
}

.search-tables-container .no-search-result {
  height: 100%;
  width: 100%;
  text-align: center;
  background: url('../img/search-empty-status.png') no-repeat;
  background-position: 310px 140px;
  background-size: 132px 132px;
  line-height: 600px;
  color: #aaa;
}

.search-result-container::-webkit-scrollbar {
  width: 0;
}

.search-tables-container .search-table-result {
  min-height: 40px;
  width: 100%;
}

.search-table-result .search-table-content {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.search-table-result .search-table-content:hover {
  cursor: pointer;
  background-color: #f3f3f3;
}

.search-table-result .search-table-name {
  max-width: 80%;
  padding-right: 12px;
  color: #202428;
  font-size: 16px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.search-table-result .search-table-name .table-views-dropdown {
  display: inline-block;
  font-size: 12px;
  transform: scale(.8) rotate(-90deg);
  transition: all .1s;
  color: #aaa;
  margin-right: 6px;
}

.search-table-result .search-table-name .table-views-dropdown.table-views-dropdown-right {
  transform: scale(.8);
}

.search-table-result .search-table-amount {
  color: #999;
  font-size: 16px;
}

.search-rows-list {
  width: 100%;
}