.calendar-dialog {
  width: 1100px;
  max-width: 1100px;
  position: absolute;
  margin-left: 50%;
  transform: translateX(-50%) !important;
  top: 0;
  bottom: 0;
}

.calendar-dialog .modal-body {
  padding: 18px 0 18px;
  overflow: hidden;
}

.calendar-dialog .modal-content {
  height: 100%;
}

.calendar-dialog .calendar-veiw-container {
  height: 100%;
}