.dtable-comment {
  border-left: 1px solid #e6e6dd;
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  overflow: auto;
}

.dtable-comment textarea:focus {
  outline: none;
}

.dtable-comment-title {
  border-bottom: 1px solid #e5e5e5;
  min-height: 3em;
  line-height: 3em;
  padding: 0 1em;
  display: flex;
  background-color: #fafaf9;
}

.dtable-comment-title .dtable-comment-title-text {
  width: 100%;
  text-align: center;
  font-weight: 700;
}

.dtable-comment-title .dtable-comment-title-close {
  color: #b9b9b9;
}

.dtable-comment-title .dtable-comment-title-close:hover {
  color: #888;
}

.dtable-comment-toggle-resolved {
  margin-top: 45px;
  border-bottom: 1px solid #e5e5e5;
  padding: 5px 10px;
  display: flex;
  position: absolute;
  background-color: #fff;
  justify-content: space-between;
  width: 29%;
}

.detail-comments .dtable-comment-list {
  height: calc(100% - 40px);
  overflow-y: auto;
  margin-bottom: 0;
  padding: 1rem 0.75rem;

}

.dtable-comment-list .comment-vacant {
  padding: 1em;
  text-align: center;
  list-style: none;
  color: #999999;
}

.dtable-comment .dtable-comment-item {
  padding: 10px 0;
  margin-bottom: 0;
}

.dtable-comment-item .dtable-comment-info {
  padding-bottom: 0.5em;
  height: 3em;
  display: flex;
  justify-content: flex-start;
}

.dtable-comment-item .dtable-comment-info .reviewer-info {
  padding-left: 10px;
  max-width: 75%;
}

.dtable-comment-item .dtable-comment-info .review-time {
  font-size: 10px;
  color: #777;
}

.dtable-comment-item .dtable-comment-info .dtable-comment-dropdown {
  margin-left: auto;
}

.dtable-comment-item .dtable-comment-info .dtable-comment-dropdown button {
  border: none;
  box-shadow: none;
  background-color: #fff;
}

.dtable-comment-item .dtable-comment-info .dtable-comment-dropdown .dtable-comment-dropdown-btn {
  color: #999;
  background-color: transparent;
}

.dtable-comment-item .dtable-comment-info .dtable-comment-dropdown:hover .dtable-comment-dropdown-btn {
  color: #555;
}

.dtable-comment-item .dtable-comment-info .dtable-comment-dropdown button:hover,
.dtable-comment-item .dtable-comment-info .dtable-comment-dropdown button:focus {
  border: none;
  box-shadow: none;
  background-color: #eee;
  cursor: pointer;
}

.dtable-comment-item .dtable-comment-content {
  margin-left: 42px;
}

.dtable-comment-item .dtable-comment-content p {
  padding: 5px 10px;
  background: #fff;
  border-radius: 4px;
  display: inline-block;
  letter-spacing: 1px;
  word-wrap: break-word;
  max-width: 100%;
}

.dtable-comment-item-resolved .dtable-comment-content p {
  background-color: #e6ffed;
}

.dtable-comment-footer {
  padding: 5px 10px;
  border-top: 1px solid #e5e5e5;
  display: flex;
  flex-direction: column;
  min-height: 185px;
  border-bottom-right-radius: 3px;
}

.dtable-comment-footer .comment-input-footer {
  padding: 5px;
  border: 1px solid #e6e6dd;
  border-top: none;
  background: #fff;
  border-radius: 0 0 5px 5px;
}

.dtable-comment-footer .comment-input-footer .comment-footer-container {
  border-top: 1px solid #e6e6dd;
}

.dtable-edit-comment .edit-comment-input {
  border: 1px solid #e6e6dd;
  padding: 5px;
  width: 23em;
  min-height: 90px;
  border-radius: 5px;
  background-color: #fff;
}

.dtable-comment-footer .submit-comment {
  margin-top: 5px;
  width: 60px;
  height: 28px;
}

.dtable-edit-comment .comment-btn {
  height: 28px;
}

@media (max-width: 768px) {
  .dtable-comment-toggle-resolved {
    width: 100%;
  }
}

.detail-comments {
  border-left: 0;
}

.detail-comments .dtable-edit-comment .edit-comment-input {
  width: 100%;
}

.add-comment-input {
  display: block;
  box-sizing: border-box;
  background-color: transparent;
  width: 100%;
  height: 90px;
  overflow: hidden;
  resize: none;
  margin: 0px;
  padding: 8px 6px;
  min-height: 90px;
  border: 1px solid rgb(230, 230, 221);
  border-radius: 5px;
}

.comment-dropdown-list {
  min-width: 8rem;
}

.comment-dropdown-item {
  padding: 0.25rem 1rem;
}

.completion-participant-avatar img {
  border-radius: 50%;
}

.comment-collaborator-tip {
  cursor: pointer;
  position: relative;
}

.comment-collaborator-tip:hover {
  color: #f19645;
}

.comment-container {
  position: fixed;
  bottom: 0;
  right: 0;
  display: flex;
  width: 300px;
  height: calc(100% - 76px);
  background-color: #f9f9f9;
  z-index: 1000;
  flex-direction: column;
  box-shadow: 0px 2px 10px 3px rgba(0, 0, 0, 0.2);
}

.comment-container .comment-header {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #dddddd;
  padding: 5px 1rem 0;
  height: 80px;
  background: #fff;
}

.comment-header .comment-header-title {
  display: flex;
  justify-content: space-between;
  height: 48px;
  align-items: center;
}

.comment-header .comment-header-tool {
  display: flex;
}

.comment-header .comment-header-tool .comment-tool-item {
  padding: 5px;
  margin-right: 10px;
  border-bottom: 2px solid transparent;
  margin-bottom: -1px;
  font-size: 14px;
  color: #666;
  cursor: pointer;
}

.comment-header .comment-header-tool .comment-item-active {
  border-bottom: 2px solid #eb8205;
  color: #212529;
}

.comment-header .comment-close {
  color: #999999;
  font-weight: bold;
  cursor: pointer;
  margin-left: 5px;
}

.comment-header .comment-close:hover {
  color: #555555;
}

.comment-popover-list {
  padding: 10px;
  max-height: 200px;
  overflow: auto;
  min-width: 150px;
}

.comment-popover-list .comment-participant-item {
  padding: 5px 0;
}

.comment-popover-list .comment-participant-item:hover {
  background: #f5f5f5;
  cursor: pointer;
}

.comment-participant-item .comment-participant-avatar {
  width: 16px;
  height: 16px;
  line-height: 16px;
  transform: translateY(-1px);
  border-radius: 50%;
  vertical-align: middle;
}

.comment-participant-item .comment-participant-name {
  margin-left: 4px;
}