.set-format-view {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #f5f5f5;
  z-index: 100;
}

.format-container {
  padding-left: 1rem;
  background-color: #fff;
  border-top: 1px solid #e9e9e9;
  border-bottom: 1px solid #e9e9e9;
}

.format-item {
  height: 50px;
  line-height: 50px;
  padding-right: 1rem;
  border-top: 1px solid #e9e9e9;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.format-item:first-child {
  border-top: none;
}

.format-check-icon {
  color: #666;
}