.participants-editor-list {
  min-height: 160px;
  width: 280px;
  left: -140px;
}

.participants-editor-list .participants-container {
  max-height: 200px;
  padding: 10px;
  overflow: auto;
}

.participants-editor-list .search-participants {
  padding: 10px;
}

.participants-editor-list .search-participants input {
  height: 28px;
}

.participants-editor-list .participant-item {
  display: flex;
  align-items: center;
  padding-left: 0;
  padding-right: 0;
}

.participants-editor-list .participant-item:hover {
  background: none;
}

.participants-editor-list .participant-container {
  display: flex;
  align-items: center;
  width: 100%;
  height: 30px;
  border-radius: 2px;
  font-size: 14px;
}

.participants-editor-list .participant-container:hover {
  background: #f5f5f5;
  cursor: pointer;
}

.participants-editor-list .participant {
  flex: 1;
}

.participant-avatar {
  border-radius: 50%;
  vertical-align: middle;
}

.participants-editor-list .participant-avatar {
  width: 16px;
  height: 16px;
  line-height: 16px;
  margin-left: 10px;
  transform: translateY(-1px);
}

.participants-editor-list .participant-name {
  margin-left: 4px;
}

.participants-editor-list .participant-check-icon {
  width: 20px;
  text-align: center;
}

.participants-editor-list .participant-check-icon .dtable-font {
  font-size: 12px;
  color: #798d99;
}

.participants-editor-list .none-search-result {
  font-size: 14px;
  opacity: .5;
  display: inline-block;
}

.participants-formmatter {
  display: inline-flex;
  align-items: center;
  overflow: hidden;
  flex-wrap: nowrap;
}

.participants-formmatter .formmatter-show {
  display: inline-flex;
  align-items: center;
  min-height: 28px;
}

.editor-list-delete .participant,
.participants-formmatter .participant {
  display: inline-flex;
  height: 20px;
  margin-right: 10px;
  padding: 0 8px 0 2px;
  font-size: 13px;
  border-radius: 10px;
  background: #eaeaea;
}

.participants-formmatter .participant-avatar-container {
  width: 16px;
}

.participants-formmatter .participant-avatar {
  width: 16px;
  height: 16px;
  transform: translateY(-1px);
}

.participants-formmatter .participant-name {
  margin-left: 5px;
}

.participants-formmatter .remove-container {
  width: 16px;
  text-align: right;
}

.participants-formmatter .remove-icon {
  display: inline-block;
  position: relative;
  right: -2px;
  width: 14px;
  height: 20px;
  cursor: pointer;
}

.participants-formmatter .remove-icon .dtable-font {
  display: inline-block;
  font-size: 12px;
  color: #909090;
  transform: scale(.8)
}