.multiple-selects-formatter {
  display: inline-flex;
  align-items: center;
  overflow: hidden;
  flex-wrap: nowrap;
}

.multiple-selects-formatter .formatter-show {
  display: inline-flex;
  align-items: center;
  min-height: 28px;
}

.multiple-selects-editor-list {
  width: 320px;
}

.multiple-selects-delete {
  background-color: #f6f6f6;
  padding: 5px;
  border-bottom: 1px solid #dde2ea;
}

.multiple-selects-delete .multiple-select {
  margin: 5px 0;
}

.multiple-selects-delete .multiple-select,
.multiple-selects-formatter .multiple-select {
  display: inline-flex;
  height: 20px;
  margin-right: 10px;
  font-size: 13px;
  border-radius: 10px;
  padding: 0 10px;
  text-align: center;
  line-height: 20px;
}

.multiple-selects-delete .multiple-select-name,
.multiple-selects-formatter .multiple-select-name {
  margin-left: 5px;
}

.multiple-selects-delete .remove-container,
.multiple-selects-formatter .remove-container {
  width: 16px;
  text-align: right;
}

.multiple-selects-delete .remove-container:hover,
.multiple-selects-formatter .remove-container:hover {
  cursor: pointer;
}

.multiple-selects-delete .remove-icon,
.multiple-selects-formatter .remove-icon {
  display: inline-block;
  position: relative;
  right: -2px;
  width: 14px;
  height: 20px;
}

.multiple-selects-delete .remove-icon .dtable-font,
.multiple-selects-formatter .remove-icon .dtable-font {
  display: inline-block;
  font-size: 12px;
  color: #909090;
  transform: scale(.8);
}