
.longtext-editor-dialog .longtext-editor-wrapper {
  height: 600px;
  overflow: hidden;
}

.longtext-formatter {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.longtext-icon-container {
  border-radius: 3px;
  display: inline-block;
}

.longtext-formatter-links-container {
  background-color: #f0f0f0;
  color: #9c9c9c;
  padding: 0 3px;
  height: 20px;
  line-height: 20px;
  margin: 0 4px;
}

.longtext-formatter-image-container {
  line-height: 30px;
  margin-right: -10px;
}

.longtext-formatter-image-container img {
  max-width: 28px;
  border: 1px solid #f0f0f0;
  max-height: 28px;
  box-sizing: border-box;
  border-radius: 3px;
}

.longtext-formatter-image-container .image-number {
  font-style: normal;
  display: inline-block;
  font-size: 12px;
  border-radius: 6px;
  background-color: #999;
  color: #fff;
  transform: translate(-50%, 9px) scale(0.8);
  min-width: 14px;
  line-height: 14px;
  padding: 0 3px;
}

.longtext-formatter-links-container .dtable-font {
  margin-right: 4px;
  font-size: 12px;
}

.longtext-formatter-checklist-completed {
  color:#61BD4F;
}

.longtext-modal {
  position: fixed;
  background-color:rgba(0, 0, 0, 0.5);
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 1049;
}

.longtext-modal-dialog {
  width: 800px;
  border-radius: 3px;
  position: absolute;
  display: flex;
  flex-direction: column;
  margin-left: 50%;
  transform: translateX(-50%);
  top: 20px;
  overflow: hidden;
  bottom: 20px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  background-color: #ffffff; 
  background-clip: padding-box;
}

.longtext-modal-dialog-header {
  height: 36px;
  line-height: 36px;
  box-sizing: border-box;
  padding: 0 8px 0 20px;
  flex: 0 0 36px;
  display: flex;
  justify-content: space-between;
}

.longtext-modal-dialog-header-border {
  border-bottom: 1px solid #e9ecef;
}

.longtext-close {
  font-size: 1.40625rem;
  font-weight: 700;
  color: #000;
  cursor: pointer;
  opacity: .5;
  width: 30px;
  text-align: center;
  display: inline-block;
}

.longtext-close:hover {
  opacity: 1;
}

.longtext-container {
  overflow: hidden;
  flex: 1 1 auto;
}

.longtext-container-scroll {
  overflow-y: auto;
  height: 100%;
}

.longtext-modal-dialog .btn {
  margin-right: 15px;
}

.longtext-container .seafile-simple-editor {
  border: none;
}

.longtext-container .article{
  padding: 10px 30px 10px 30px;
  height: auto;
}

.longtext-container .seafile-rich-editor-topbar {
  height: 34px;
  padding-left: 10px; 
  border-top: 1px solid #e9ecef;
  border-bottom: 1px solid #e9ecef; 
  box-shadow: none;
}

.longtext-container .sf-editor-rich-menu {
  height: 32px;
} 

.longtext-container .rich-icon-btn {
  line-height: 24px;
}

.longtext-container .header-list-container {
  height: 32px;
  padding: 4px 5px;
  border-right: 1px solid #e9ecef;
}

.longtext-container .list-body div {
  line-height: 24px;
}

.longtext-container .editor-btn-group {
  padding: 3px 0 3px 5px;
  border-right: 1px solid #e9ecef;
}

.longtext-container .upload-localimg {
  height: 26px;
}

.upload-localimg .btn {
  height: 26px;
}

.longtext-container .custom-dropdown-list .dropdown-list-toggle {
  height: 24px;
}

.longtext-container h2 {
  border-bottom: none;
}