.dtable-plugins-module {
  position: fixed;
  bottom: 0;
  right: 0;
  display: flex;
  width: 300px;
  height: calc(100% - 76px);
  background-color: #f9f9f9;
  z-index: 1000;
  flex-direction: column; 
  box-shadow: 0px 2px 10px 3px rgba(0, 0, 0, 0.2);
}

.dtable-plugins-module .dtable-plugins-header {
  position: relative;
  height: 48px;
  background-color: #ffffff;
  border-bottom: 1px solid #dddddd;
  user-select: none;
}

.dtable-plugins-module .dtable-plugins-body {
  flex: 1;
  display: flex;
}

.dtable-plugins-module .dtable-plugins-footer {
  padding: 10px 10px 20px;
  height: 50px;
}

.dtable-plugins-header .pligins-title {
  height: 48px;
  line-height: 48px;
  text-align: center;
  font-size: 14px;
}

.dtable-plugins-header .plugins-close {
  position: absolute;
  top: 0;
  right: 5px;
  height: 48px;
  display: flex;
  align-items: center;
  color: #999999;
  font-weight: bold;
  cursor: pointer;
}

.dtable-plugins-header .plugins-close:hover {
  color: #555555;
}

.dtable-plugins-body .dtable-plugins-list {
  display: flex;
  flex: 1;
  flex-direction: column;
  padding: 10px 0;
}

.dtable-plugins-body .dtable-plugins-list .plugin-panel-item {
  display: flex;
  padding: 8px;
  height: 40px;
}

.dtable-plugins-body .dtable-plugins-list .plugin-panel-item:hover {
  background-color: #eee;
  cursor: pointer;
}

.dtable-plugins-body .plugin-panel-item .item-info {
  display: flex;
  flex: 1;
}

.dtable-plugins-body .plugin-panel-item .item-icon {
  margin-right: 4px;
}

.dtable-plugins-body .plugin-panel-item .item-name {
  flex: 1;
  display: flex;
  align-items: center;
  font-size: 16px;
}

.dtable-plugins-body .plugin-panel-item .dropdown .dtable-icon-more-level {
  font-size: 14px;
  color: #999;
}

.dtable-plugins-body .plugin-panel-item .dropdown .dtable-icon-more-level:hover {
  color: #333;
}

.dtable-plugins-manager-dialog {
  max-width: 942px;
}

.dtable-plugins-manager-content {
  width: 100%;
}

.plugins-manager-header {
  position: relative;
  padding: 0;
}

.plugins-manager-header .modal-title {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.plugins-manager-header .header-title {
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 1rem;
  padding-left: 20px;
}

.plugins-manager-header .header-nav {
  display: flex;
}

.plugins-manager-header .header-nav-item {
  padding: 1rem 0;
  margin-right: 30px;
  border-bottom: 2px solid #fafafa;
  font-size: 14px;
  color: #999999
}

.plugins-manager-header .header-nav-item:hover {
  cursor: pointer;
}

.plugins-manager-header .header-nav-item.active {
  border-bottom: 2px #F09F3F solid;
  color: #F09F3F;
}

.plugins-manager-header .close {
  position: absolute;
  right: 0;
  margin: 0;
}

.plugins-manager-container {
  display: flex;
  flex-wrap: wrap;
  margin-left: -20px;
  padding: 20px 30px;
  height: 500px;
  overflow: auto;
}

.plugins-manager-container .plugin-item {
  height: 220px;
  width: 280px;
  margin-left: 20px;
  margin-bottom: 20px;
  border-radius: 2px;
  border: 1px solid #eee;
  position: relative;
}

.plugins-manager-container .plugin-item .plugin-version {
  position: absolute;
  background: #fff;
  padding: 3px;
  border-radius: 3px;
  right: 16px;
  top: 10px;
  font-size: 13px;
  color: #666666;
  height: 24px;
}

.plugins-manager-container .plugin-item .plugin-item-mask {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 2px;
  position: absolute;
  top: 0;
}

.plugins-manager-container .plugin-item .plugin-item-icon {
  width: 100%;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #eee;
}
 
.plugins-manager-container .plugin-item .plugin-item-info {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100px;
  padding: 10px;
}

.plugin-item .plugin-item-info .item-content {
  display: flex;
  padding: 10px;
}

.plugin-item .plugin-item-info .item-content .item-name {
  flex: 1;
  display: flex;
  align-items: center;
  font-size: 16px;
}

.plugin-item .plugin-item-info .item-content .item-name:hover {
  text-decoration: underline;
  cursor: pointer;
}

.plugin-item .plugin-item-info .item-description {
  padding: 0 10px 10px;
  font-size: 13px;
  color: #999;
}

.plugin-item .file-uploader {
  position: fixed;
  top: -100px;
  width: 0;
  height: 0;
}

.plugins-manager-container .plugin-upload-item {
  height: 220px;
  width: 280px;
  margin-left: 20px;
  margin-bottom: 20px;
  border-radius: 2px;
  border: 1px solid #eee;
  position: relative;
}

.plugins-manager-container .plugin-item-mask {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 2px;
  position: absolute;
}

.plugins-manager-container .plugin-upload-wrapper {
  z-index: 2;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0
}

.plugin-upload-wrapper .upload-success-tip {
  color: #fff;
  z-index: 2;
}

.plugin-upload-wrapper .plugin-item-progress {
  transform: rotate(90deg) scale(0.8);
  color: #fff !important;
}

.plugins-manager-container .register-plugin-item {
  display: flex;
  height: 220px;
  width: 280px;
  margin-left: 20px;
  margin-bottom: 20px;
  border-radius: 2px;
  border: 1px solid #eee;
}

.plugins-manager-container .register-plugin-item.plugin-item-active {
  border: 1px solid #F09F3F;
}

.register-plugin-item .register-plugin-container {
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.register-plugin-item .register-plugin-container .register-plugin-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


.register-plugin-item .register-plugin-container .register-plugin-content .register-plugin-icon {
  color: #999;
  font-size: 36px;
}

.register-plugin-item .register-plugin-container .register-plugin-content .register-plugin-tip {
  font-size: 16px;
}

.register-plugin-item .register-plugin-container .file-uploader {
  position: fixed;
  top: -100px;
  width: 0;
  height: 0;
}

.plugins-manager-container .no-content-tip {
  width: 100%;
  margin: auto;
  text-align: center;
  font-size: 22px;
  color: #999;
}
