#header {
  position: relative;
  display: flex;
  text-align: center;
  border-bottom: 1px solid #F09F3F;
  background-color: #F09F3F;
  font-size: 16px;
  height: 50px;
  justify-content: space-between;
  padding: 0 8px;
}

#main {
  display: flex;
  flex: 1;
}

#footer {
  position: relative;
  background-color: #F3F3F3;
  height: 32px;
  line-height: 32px;
  border-top: 1px solid #ddd;
  z-index: 2;
}

/* header */

.logo {
  margin-left: 16px;
  display: flex;
  align-items: center;
  position: relative;
}
.logo .dtable-icon-dtable-logo {
  line-height: 24px;
  font-size: 24px;
  color: #fff;
}

.logo .dtable-logo-link {
  text-decoration: none;
}

.logo .tip-message {
  margin-left: 12px;
  font-size: 12px;
  color: #FFD6A6;
  position: absolute;
  left: 100%;
  width: max-content;
  display: inline-block;
  white-space: nowrap;
}

.logo .dtable-icon-check-mark {
  color: rgba(255, 255, 255, 0.7);
  font-size: 12px;
  position: absolute;
  left: 100%;
}

#header .title {
  height: 50px;
  line-height: 50px;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
}

#header .tools {
  cursor: pointer;
  margin-right: 14px;
  display: flex;
  color: #fff;
  align-items: center;
}

#header .form {
  color: #fff;
}

#header .statistics .dtable-font {
  color: #fff;
}

#header .statistics .dtable-font:hover {
  color: #ffeeda
}

#header .tools .dtable-font {
  margin-right: 4px;
}

#header .tools .tool-title {
  font-size: 12px;
}

#header .tool {
  display: flex;
  line-height: 16px;
  margin-right: 14px;
}

#header .tool:last-child {
  margin-right: 0;
}

#header .notification {
  position: relative;
}

#header .notification .notification-count {
  position: absolute;
  color: #fff;
  font-size: 12px;
  top: -10px;
  right: -4px;
  padding: 2px;
  background: #fc6440;
  border-radius: 50%;
  min-width: 20px;
  min-height: 20px;
  transform: scale(0.7);
}

#header .export-dtable {
  margin-right: 14px;
}

#header .journal {
  margin-right: 16px; 
}

.btn-seafile {
  padding-right: 8px;
  border-radius: 2px;
  width: 50px;
  height: 24px;
  margin-right: 8px;
  background-color: #fff;
  color: #F09F3F;
  font-size: 14px;
  border: none;
  outline: none !important;
}

.btn-seafile:hover {
  color: #F09F3F;
  background-color: #FFEEDA;
}

.btn-seafile:disabled, .btn-seafile.disabled {
  color: #F09F3F;
  background-color: #FFD29E;
}

/* main */
.drag-drop-container {
  display: flex;
  flex: 1;
}

.react-grid-Canvas {
  background-color: #F3F3F3;
}

.react-grid-Grid {
  border: none;
}

.react-grid-checkbox-label {
  font-size: 0;
}

.react-grid-add-row-icon,
.react-grid-add-row-item {
  border-right: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  background-color: #FFFFFF;
}

.group-item.expanded-group .react-grid-add-row-icon {
  position: unset !important;
  border-left: 1px solid hsla(0,0%,0%,0.15);
  border-bottom: 1px solid hsla(0,0%,0%,0.15);
  border-bottom-left-radius: 5px;
  overflow: hidden;
}

.group-item.frozen .react-grid-add-row-icon {
  z-index: 2;
}

.group-item.expanded-group .react-grid-add-row-item {
  border-right: 1px solid hsla(0,0%,0%,0.15);
  border-bottom: 1px solid hsla(0,0%,0%,0.15);
  border-bottom-right-radius: 5px;
  overflow: hidden;
}

/* summarys */
.summarys {
  position: relative;
  display: flex;
}

.summarys .rows-record {
  width: 80px;
  height: 32px;
  padding-left: 8px;
  line-height: 32px;
  white-space: nowrap;
}

.summarys .summarys-pane {
  position: relative;
  display: flex;
  flex: 1 1;
  overflow: hidden;
}

.summarys .summarys-scroll {
  position: relative;
  overflow: hidden;
}

.summarys .summarys-scroll>div {
  display: inline-flex;
}

.summarys .summary-item {
  padding: 0 8px;
  text-align: right;
}

.summarys .summary-item-title {
  color: #949494;
}

.summarys .summary-content {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}