.react-grid-Cell {
  background-color: #ffffff;
  padding-left: 8px;
  padding-right: 8px;
  border-right: 1px solid #eee;
  border-bottom: 1px solid #dddddd;
  font-size: 14px;
  color: #202428;
}

.react-grid-Cell.row-comment-cell {
  display: flex;
  justify-content: center;
  align-items: center;
}

.row-comment-cell .row-comment-content {
  color: #ccc;
  margin-left: 8px;
  margin-right: -8px;
  cursor: pointer;
}

.row-comment-cell .row-comment-content .row-comment-icon {
  margin-right: -9px;
}

.row-comment-cell .row-comment-content .row-comment-count {
  color: #fff;
  transform: scale(0.5) translateY(5px);
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  min-width: 25px;
  min-height: 25px;
  background: #969696;
  margin-left: -3px;
  font-size: 16px;
}

.group-list .group-item .rdg-row-actions-cell {
  border-left: 1px solid hsla(0,0%,0%,0.15);
}

.group-list .group-item.frozen .rdg-row-actions-cell {
  z-index: 2;
}

.group-list .group-item.read .last-row .react-grid-Cell {
  border-bottom: 1px solid hsla(0,0%,0%,0.15);
}

.group-list .group-item.read .last-row .rdg-row-actions-cell {
  border-bottom-left-radius: 5px;
}

.group-list .group-item.read .last-row .last-cell {
  border-bottom-right-radius: 5px;
}

.group-list .react-grid-Cell.last-cell {
  border-right: 1px solid hsla(0,0%,0%,0.15);
}

.row-cell-selected .react-grid-Cell.cell-selected {
  background: #fff;
}

/* .react-grid-Cell:hover {
  cursor: pointer;
} */

.rdg-selected {
  border: 2px solid #66afe9;
}

.rdg-selected-range {
  border: 1px solid #66afe9;
  background-color: #66afe930;
}

.moving-element {
  will-change: transform;
}
.react-grid-Cell--frozen {
  /* Should have a higher value than 1 to show in front of cell masks */
  z-index: 2;
}
.rdg-last--frozen, .react-grid-HeaderCell--frozen {
  border-right: 1px solid #dddddd;
  box-shadow: 2px 0 5px -2px rgba(136, 136, 136, .3);
}

/* cell which have tooltips need to have a higher z-index on hover so that the tooltip appears above the other cells*/
.react-grid-Cell.has-tooltip:hover {
  z-index: 1;
}

.react-grid-Cell.react-grid-Cell--frozen.has-tooltip:hover  {
  z-index: 3
}

.react-contextmenu--visible {
  z-index: 1000;
}

.react-grid-Cell:not(.editing) .react-grid-Cell__value {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  height: inherit;
}

.row-comment-cell .react-grid-Cell__value {
  flex: 1;
}

.default-text-row-cell .grid-cell-type-default {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.react-grid-Cell__value .grid-cell-type-number {
  text-align: right;
}

.react-grid-Cell.readonly {
  background-color : black;
}

.react-grid-Row .rdg-row-expand-icon {
  display: none;
  position: absolute;
  top: -10px;
  right: 5px;
  min-width: 20px;
  min-height: 20px;
  border-radius: 50%;
  text-align: center;
  line-height: 20px;
}

.react-grid-Row:hover .rdg-row-expand-icon {
  display: inline;
}

.react-grid-Row .rdg-row-expand-icon:hover {
  background: #c2f5e9;
}

.react-grid-Row .rdg-row-expand-icon i {
  font-size: 12px;
  color:#467fcf;
}

.react-grid-cell .form-control-feedback {
  color: #a94442;
  position: absolute;
  top: 0px;
  right: 10px;
  z-index: 1000000;
  display: block;
  width: 34px;
  height: 34px;
}

.react-grid-Row.row-selected .react-grid-Cell{
 background-color: #DBECFA;
}

.react-grid-Cell.editing {
  padding: 0;
  overflow: visible !important;
}

.react-grid-Cell.editing .has-error input {
  border: 2px red solid !important;
  border-radius: 2px !important;
}

.react-grid-Cell__value ul {
  margin-top: 0;
  margin-bottom: 0;
  display: inline-block;
}

.react-grid-Cell__value .btn-sm {
  padding: 0;
}

.react-grid-Cell__value .addition-image {
  border: 1px solid #f0f0f0;
  height: 28px;
  display: inline-block;
  vertical-align: middle;
  font-size: 14px;
  line-height: 28px;
  padding: 0 2px;
  color: #ccc;
  border-radius: 2px;
}

.react-grid-Cell__value .addition-image:hover {
  background: #f6f6f6;
  border: 1px solid #c9c9c9;
}

.cell-tooltip .cell-tooltip-text {
    white-space: normal;
    visibility: hidden;
    width: 150px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    top: 50%;
    bottom: initial;
    left: 50%;
    margin-top: 15px;
    margin-left: -75px
}

.cell-tooltip:hover .cell-tooltip-text {
    visibility: visible;
    opacity: 0.8;
}

.cell-tooltip .cell-tooltip-text::after {
    content: " ";
    position: absolute;
    bottom: 100%;  /* At the top of the tooltip */
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent black transparent;
}

.cell-highlight {
  background-color: rgba(240, 159, 63, 0.6);
}

.cell-current-highlight {
  background-color: #F09F3F;
}

.react-grid-Canvas.opaque .react-grid-Cell.cell-tooltip:hover .cell-tooltip-text {
    visibility: hidden;
}

.rdg-cell-expand {
    float: right;
    display: table;
    height: 100%;
}

.rdg-cell-expand > span {
  display: table-cell;
  vertical-align: middle;
  cursor: pointer;
}

.rdg-child-row-action-cross:before, rdg-child-row-action-cross:after, .rdg-child-row-action-cross-last:before, rdg-child-row-action-cross-last:after {
  content: "";
  position: absolute;
  background: grey;
  height: 50%;
}

.rdg-child-row-action-cross:before {
  left: 21px;
  width: 1px;
  height: 100%;
}

.rdg-child-row-action-cross-last:before {
  left: 21px;
  width: 1px;
}

.rdg-child-row-action-cross:after, .rdg-child-row-action-cross-last:after {
  top: 50%;
  left:20px;
  height: 1px;
  width: 15px;
  content: "";
  position: absolute;
  background: grey;
}

.rdg-child-row-action-cross:hover {
    background: red;
}
.rdg-child-row-btn {
   position:absolute;
   cursor:pointer;
   border:1px solid grey;
   border-radius:14px;
   z-index: 3;
   background: white;
}

.rdg-child-row-btn div {
  font-size: 12px;
  text-align:center;
  line-height:19px;
  color: grey;
  height:20px;
  width:20px;
  position:absolute;
  top:60%;
  left:53%;
  margin-top:-10px;
  margin-left:-10px;

}

.rdg-empty-child-row:hover .glyphicon-plus-sign {
  color: green;
}

.rdg-empty-child-row:hover a {
  color: green;
}

.rdg-child-row-btn .glyphicon-remove-sign:hover {
  color: red;
}

.last-column .cell-tooltip-text{
  right: 100%;
  left: 0% !important;
}

.rdg-cell-action {
  float: right;
  height: 100%;
}

.rdg-cell-action-last {
  margin-right: -8px;
}

.rdg-cell-action-button {
  width: 35px;
  height: 100%;
  text-align: center;
  position: relative;
  display: table;
  color: #4a9de2;
}

.rdg-cell-action-button > span {
  display: table-cell;
  vertical-align: middle;
}

.rdg-cell-action-button:hover, .rdg-cell-action-button-toggled {
  color: #447bbb;
}

.rdg-cell-action-menu {
  position: absolute;
  top: 100%;
  z-index: 1000;
  float: left;
  min-width: 160px;
  padding: 5px 0;
  text-align: left;
  list-style: none;
  background-color: #fff;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  border: 1px solid #ccc;
  box-shadow: 0 0 3px 0 #ccc;
}

.rdg-cell-action-menu > span {
  display: block;
  padding: 3px 10px;
  clear: both;
  font-weight: 400;
  line-height: 1.42857143;
  color: #333;
  white-space: nowrap;
}

.rdg-cell-action-menu > span:hover {
  color: #262626;
  text-decoration: none;
  background-color: #f5f5f5;
}

.group-list {
  position: relative;
  overflow: hidden;
}

.group-list .group-item .group-backdrop {
  position: absolute;
  left: 0;
  height: 100%;
  background: #f3f3f3;
}

.group-list .group-item {
  position: relative;
  left: 12px;
  padding-top: 12px;
}

.group-list .group-item-main {
  position: relative;
  overflow: hidden;
}

.group-list .group-header {
  position: relative;
  display: flex;
  height: 42px;
}

.group-list .header-left-pane {
  position: relative;
  display: flex;
  align-items: center;
  border-left: 1px solid hsla(0,0%,0%,0.15);
  border-top: 1px solid hsla(0,0%,0%,0.15);
  border-right: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  border-top-left-radius: 5px;
  background: #f3f3f3;
}

.group-list .group-item.folded-group .header-left-pane {
  border-bottom-left-radius: 5px;
  border-bottom: 1px solid hsla(0,0%,0%,0.15);
}

.group-list .group-item.folded-group .header-right-pane {
  border-bottom-right-radius: 5px;
}

.group-list .group-expand {
  padding: 0 .5rem;
}

.group-list .group-expand span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 18px;
  height: 18px;
}

.group-list .group-expand span:hover {
  cursor: pointer;
}

.group-list .group-expand-icon {
  font-size: 12px;
}

.group-list .group-title {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.group-list .group-title .group-column-name {
  font-size: 12px;
  color: #949494;
  font-weight: 500;
}

.group-list .group-title .group-cell-value {
  display: flex;
  font-weight: 500;
}

.group-list .group-rows-count {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  display: inline-flex;
  align-items: center;
  padding: 0 15px 0 20px;
  background: linear-gradient(to right, hsla(0,0%,97%,0), hsl(0,0%,97%) 18%);
}

.group-list .group-rows-count .count-title {
  margin-right: 4px;
  color: #949494;
}

.group-list .rows {
  position: relative;
}

.group-list .header-right-pane {
  position: relative;
  display: inline-flex;
  border-top: 1px solid hsla(0,0%,0%,0.15);
  border-right: 1px solid hsla(0,0%,0%,0.15);
  border-top-right-radius: 5px;
  overflow: hidden;
}

.group-list .summary-item {
  position: absolute;
  height: 100%;
  padding: 0 8px;
  border-right: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.group-list .folded-group .summary-item {
  border-bottom: 1px solid hsla(0,0%,0%,0.15);
}

.group-list .summary-item:last-child {
  border-right: none;
}

.group-list .summary-item .summary-content {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.group-list .summary-item .summary-item-title {
  color: #949494;
}

.react-grid-Cell .cell-file-add {
  height: 31px;
  line-height: 31px;
  text-align: center;
  color: #999;
  box-sizing: border-box;
  position: relative;
  top: 0px;
  left: -8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.cell-selected .cell-file-add {
  height: 28px !important;
  line-height: 28px;
  top: 2px;
  left: -5px;
}

.draging-file-to-cell .cell-file-add {
  border: 1px dashed #f09f3f;
  background-color: #fdf5eb !important;
}

.react-grid-Cell__value .cell-uploading-file {
  display: inline-block;
  height: 28px;
  width: 28px;
  vertical-align: middle;
  border: 1px solid #f0f0f0;
  border-radius: 2px;
  margin-right: 4px;
  background-color: rgba(255, 255, 255, .7);
  position: relative;
}

.react-grid-Cell__value .cell-uploading-file .cell-upload-percent {
  height: 20px;
  width: 20px;
  position: absolute;
  top: 0; 
  bottom: 0;
  left: 0; 
  right: 0;
  margin: auto;
}

.react-grid-Cell__value .cell-upload-percent svg {
  vertical-align: baseline !important;
}

.react-grid-Cell__value .cell-upload-percent .file-upload-span {
  color: #999999 !important;
  font-weight: 600;
}

.cell-toast .file-upload-again {
  color: #cd201f;
}