.rdg-selected {
  border: 2px solid #66afe9;
}

.rdg-selected .drag-handle,
.rdg-selected-range .drag-handle,
.checkbox-editor-container .drag-handle {
  pointer-events: auto;
  position: absolute;
  bottom: -5px;
  right: -4px;
  background: #66afe9;
  width: 8px;
  height: 8px;
  border: 1px solid #fff;
  border-right: 0px;
  border-bottom: 0px;
  cursor: crosshair;
  cursor: -moz-grab;
  cursor: -webkit-grab;
  cursor: grab;
}

.rdg-selected .drag-handle:hover, 
.rdg-selected-range .drag-handle:hover,
.checkbox-editor-container .drag-handle:hover {
  bottom: -8px;
  right: -7px;
  background: white;
  width: 16px;
  height: 16px;
  border: 1px solid #66afe9;
  z-index: 2;
}

.rdg-selected:hover .drag-handle .glyphicon-arrow-down {
  display: 'block'
}

.rdg-selected .editor-expand-handle {
  pointer-events: auto;
  position: absolute;
  right: 0;
  top: 0;
  color: #467fcf;
  border-radius: 50%;
  cursor: pointer;
  background: #fff;
  min-width: 20px;
  min-height: 20px;
  line-height: 20px;
  text-align: center;
}

.rdg-selected .editor-expand-handle:hover {
  background: #c2f5e9;
}

.rdg-selected .editor-expand-handle .dtable-icon-open {
  font-size: 12px;
}

.react-grid-cell-dragged-over-up, .react-grid-cell-dragged-over-down {
  border: 1px dashed black;
  background: rgba(0, 0, 255, 0.2) !important;
}

.react-grid-cell-dragged-over-up {
  border-bottom-width: 0;
}

.react-grid-cell-dragged-over-down {
  border-top-width: 0;
}

.rdg-editor-container input.editor-main, select.editor-main {
  display: block;
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #555555;
  background-color: #ffffff;
  background-image: none;
}

.rdg-editor-container input.editor-main::-moz-placeholder, select.editor-main::-moz-placeholder {
  color: #999999;
  opacity: 1;
}

.rdg-editor-container input.editor-main:-ms-input-placeholder, select.editor-main:-ms-input-placeholder {
  color: #999999;
}

.rdg-editor-container input.editor-main::-webkit-input-placeholder, select.editor-main::-webkit-input-placeholder {
  color: #999999;
}

.rdg-editor-container input.editor-main[disabled], select.editor-main[disabled], .rdg-editor-container input.editor-main[readonly], select.editor-main[readonly], fieldset[disabled] .rdg-editor-container input.editor-main, fieldset[disabled] select.editor-main {
  cursor: not-allowed;
  background-color: #eeeeee;
  opacity: 1;
}

textarea.rdg-editor-container input.editor-main, textareaselect.editor-main {
  height: auto;
}


.mobile-edit-cell-icon,
.mobile-insert-row-icon {
  position: fixed;
  width: 50px;
  height: 50px;
  font-size: 16px;
  border-radius: 50%;
  box-shadow: 0 3px 5px -1px rgba(292, 162, 82, .2), 0 6px 10px 0 rgba(292, 162, 82, .14), 0 1px 18px 0 rgba(292, 162, 82, .12);
  user-select: none;
  border: none;
  right: 10px;
  bottom: 42px;
  color: #999;
  background-color: rgb(292, 162, 82);
  cursor: pointer;
  display: flex;
}

.mobile-edit-cell-icon i,
.mobile-insert-row-icon i {
  position: absolute;
  top: 12px;
  left: 15px;
  font-size: 20px;
  font-weight: 700;
  color: white;
}
