.duplicate-table-info {
  display: inline-flex;
}

.duplicate-table-info .duplicate-table {
  padding-right: 10px;
  font-size: 14px;
  flex-shrink: 0;
}

.duplicate-table-info .duplicate-table-name {
  font-size: 14px;
  flex-shrink: 0;
  max-width: 350px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.duplicate-table-container {
  min-height: 80px;
  box-sizing: border-box;
  padding: 15px;
  display: flex;
  align-items: center;
}

.duplicate-table-container .form-group {
  margin: 0;
}

.duplicate-table-container .duplicate-records-name {
  font-size: 14px;
}