.geolocation-editor-container {
  background-color: #ffffff;
  box-shadow: 0 0 5px #ccc;
  border-radius: 4px;
  position: relative;
  display: inline-block;
  width: 400px;
}

.geolocation-editor-content {
  box-sizing: border-box;
}

.geolocation-editor-content .geolocation-editor-item {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

.geolocation-editor-item-left {
  flex: 0 0 80px;
}

.geolocation-editor-item-right {
  flex-basis: 1 1 auto;
}

.geolocation-editor-selector-container {
  line-height: 30px;
  position: relative;
  box-sizing: content-box;
}

.geolocation-editor-selector {
  width: 300px;
  height: 30px;
  line-height: 30px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.geolocation-editor-selector {
  display: flex;
  padding: 0 10px;
  border-radius: 3px;
  justify-content: space-between;
  cursor: pointer;
  overflow: hidden;
  flex-wrap: nowrap;
  align-items: center;
  color: #495057;
  justify-content: space-between;
  background: #fff;
  word-break: keep-all;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}

.geolocation-editor-parameter-icon {
  color: #747474;
  font-size: 12px;
}

.geolocation-editor-detail-info {
  width: 300px;
  padding: 5px;
  display: inline-block;
  height: 70px;
  resize: none;
  border-radius: 3px;
  border: 1px solid #ccc;
  color: #495057;
}

.geolocation-editor-container .geolocation-selector-container {
  position: absolute;
  min-width: 400px;
  top: 100%;
  left: 0;
  background-color: #ffffff;
  box-shadow: 0 0 5px #ccc;
  min-height: 165px;
}

.geolocation-editor-container .geolocation-selector-header {
  height: 45px;
  display: flex;
  border-bottom: 1px solid #ccc;
  padding: 5px 20px 0 20px;
  align-items: flex-end;
}

.geolocation-editor-container .geolocation-selector-header-item {
  border: 1px solid #ccc;
  height: 35px;
  margin-right: 10px;
  display: flex;
  margin-bottom: -1px;
  border-radius: 3px 3px 0 0;
  padding: 10px;
  line-height: 15px;
  cursor: pointer;
  font-size: 14px;
  word-break: keep-all;
}

.geolocation-selector-header-item i {
  color: #747474;
  transform: scale(0.8);
  display: inline-block;
}

.geolocation-editor-container .selected-geolocation-selector-header-item {
  border-bottom: 1px solid #fff;
}

.geolocation-selector-header-item span {
  display: inline-block;
}

.selected-geolocation-selector-header-item span {
  color: #F09F3F;
}

.geolocation-selector-list-item {
  padding: 0 8px;
  text-align: center;
  font-size: 14px;
  line-height: 30px;
  display: inline-block;
  height: 30px;
  cursor: pointer;
}

.geolocation-selector-list {
  padding: 0;
  display: flex;
  width: fit-content;
  flex-wrap: wrap;
}

.geolocation-selector-list .selected-list-item {
  color: #F09F3F;
}