.row-expand-view {
  height: 100%;
  width: 100%;
  background-color: #f5f5f5;
  position: absolute;
}

.row-expand-view .am-list-header {
  border-bottom: 1px solid #e9e9e9;
  background-color: #fff;
  padding: 0;
}

.am-list-header .row-expand-view-header {
  display: flex;
  justify-content: space-between;
  height: 50px;
}

.am-list-header .row-expand-view-header .row-expand-view-header-btn {
  min-width: 50px;
  padding: 13px 1rem;
}

.am-list-header .row-expand-view-header .row-expand-view-header-title {
  margin-bottom: 0;
  color: #333;
  line-height: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.row-expand-view .row-expand-addition-image {
  margin-bottom: 1rem;
}

.row-expand-view .row-expand-addition-image, .row-expand-view .row-expand-btn {
  color: #929292;
}

html:not([data-scale]) .am-list-body::before,
html:not([data-scale]) .am-list-body::after {
  display: none;
}

.am-picker-popup-header .am-picker-popup-item {
  color: #E5A252;
}

.am-list-body.expand-row-container {
  padding: 1rem;
  overflow-y: scroll;
  width: 100%;
  padding-top: 0;
  padding-bottom: 0;
  overflow-y: auto;
  height: calc(100% - 51px);
}

.row-expand-view::-webkit-scrollbar,
.single-select-view .select-options-body::-webkit-scrollbar,
.multiple-select-view .select-options-body::-webkit-scrollbar,
.long-text-view .long-text-container .am-textarea-item textarea::-webkit-scrollbar,
.long-text-view .long-text-container::-webkit-scrollbar,
.am-list-body.expand-row-container::-webkit-scrollbar {
  width: 0;
}

.row-expand-item-title {
  background-color: #f5f5f5;
  border-bottom: 1px solid #e9e9e9;
  border-top: 1px solid #e9e9e9;
  margin: 0 -1rem;
  padding: 0.625rem 1rem 0;
}

.row-expand-view .expand-row-container .row-expand-item {
  margin-bottom: 0px;
}

.row-expand-view .expand-row-container .row-expand-item:first-child .row-expand-item-title {
  border-top: 0px;
}

.am-list-body .row-expand-item .row-expand-item-formula,
.am-list-body .row-expand-item .simple-line-text {
  height: 50px;
  line-height: 50px;
}

.am-list-body .row-expand-item .simple-line-text>label {
  display: none;
}

.am-list-body .row-expand-item .simple-line-text input {
  border: none;
  padding: 0;
  height: 50px;
}

.am-list-body .row-expand-item .simple-line-text .form-control:focus {
  box-shadow: none;
}

.am-list-body .row-expand-item .multiple-select-option {
  margin: 0 5px 5px 0;
  max-width: 150px;
}

.am-list-body .row-expand-item .single-select-option {
  margin: 0;
}

.am-list-body .row-expand-item .row-expand-item-angle-right {
  color: #999;
  position: absolute;
  right: 0;
  top: calc(50% - 10px);
}

.row-expand-view .am-whitespace {
  background-color: #f5f5f5;
}

.am-list-body .row-expand-links,
.am-list-body .row-expand-collaborators,
.am-list-body .row-expand-item .options-list {
  margin: 1rem 0;
  min-height: 18px;
}

.single-select-view {
  background-color: #f5f5f5;
}

.single-select-view .select-options-body, .multiple-select-view .select-options-body {
  overflow-y: scroll;
}

.select-options-body .current-cell-value-container .view-subtitle {
  border-top: 0;
}

.select-options-body .current-cell-value-container .current-cell-value {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid #e9e9e9;
  box-sizing: border-box;
  background-color: #fff;
  padding: 7px 16px;
  display: inline-flex;
  align-items: center;
}

.select-options-body .current-cell-value .current-option-container {
  display: inline-flex;
  height: 20px;
  font-size: 13px;
  border-radius: 10px;
  padding: 0 10px;
  text-align: center;
  line-height: 20px;
}

.multiple-select-view .select-options-body .current-cell-value-container {
  height: auto;
}

.multiple-select-view .select-options-body .current-cell-value {
  min-height: 50px;
  padding: 4px 16px 7px;
  height: auto;
  display: inline-flex;
  flex-wrap: wrap;
}

.multiple-select-view .current-cell-value .empty-placeholder {
  line-height: 36px;
}

.multiple-select-view .select-options-body .current-multiple-select-item {
  margin: 3px 0; 
  height: 20px;
  flex-shrink: 0;
}

.multiple-select-view .multiple-select-item .current-option-container {
  margin-right: 10px;
}

.select-options-body .current-option-container .current-option-name {
  white-space:nowrap;
  max-width: 150px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.select-options-body .current-option-container .remove-container {
  width: 18px;
  text-align: right;
}

.select-options-body .current-option-container .remove-option {
  display: inline-block;
  font-size: 12px;
  color: #909090;
  transform: scale(.8);
}

.single-select-view .search-single-selects, .multiple-select-view .search-multiple-selects {
  padding: 0.5rem 1rem;
  background-color: #fff;
}

.links-editor-view .search-links .form-control,
.collaborator-editor-view .search-collaborators .form-control,
.single-select-view .search-single-selects .form-control,
.multiple-select-view .search-multiple-selects .form-control {
  border: none;
  padding: 0;
}

.links-editor-view .search-links .form-control:focus,
.collaborator-editor-view .search-collaborators .form-control:focus,
.edit-option-view .form-control:focus,
.single-select-view .search-single-selects .form-control:focus,
.multiple-select-view .search-multiple-selects .form-control:focus {
  box-shadow: none;
}

.single-select-view .search-single-selects .single-select-item,
.multiple-select-view .search-multiple-selects .multiple-select-item  {
  border: 1px solid #e9e9e9;
}

.links-editor-view .none-search-result,
.single-select-view .none-search-result,
.multiple-select-view .none-search-result,
.collaborator-editor-view .none-search-result {
  padding: 1rem 0;
}

.single-select-view .single-selects-editor-list,
.multiple-select-view .multiple-selects-editor-list {
  width: 100%;
  box-shadow: none;
  border-radius: 0;
  min-height: auto;
  background-color: #f5f5f5;
  border-left: 0;
  border-right: 0;
  border-color: #e9e9e9;
}

.single-select-view .single-selects-editor-list .single-selects-container,
.multiple-select-view .multiple-selects-editor-list .multiple-selects-container {
  overflow: visible;
  background-color: #fff;
  max-height: none;
  min-height: auto;
  padding: 0 0 0 1rem;
}

.single-select-view .single-selects-editor-list .single-selects-container:hover, 
.multiple-select-view .multiple-selects-editor-list .multiple-selects-container:hover {
  background-color: #fff;
}

.single-select-view .single-selects-editor-list .single-select-item,
.multiple-select-view .multiple-selects-editor-list .multiple-select-item {
  border-top: 1px solid #e9e9e9;
}

.single-select-view .single-selects-editor-list .single-select-item:first-child,
.multiple-select-view .multiple-selects-editor-list .multiple-select-item:first-child {
  border-top: none;
}

.single-select-view .single-selects-editor-list .single-select-item:last-of-type, 
.multiple-select-view .multiple-selects-editor-list .multiple-select-item:last-of-type {
  margin-bottom: 0;
  border-bottom: 0;
}

.single-select-view .single-selects-editor-list .single-select-container,
.multiple-select-view .multiple-selects-editor-list .multiple-select-container {
  padding: 0;
  margin: 10px 10px 10px 0px;
}

.single-select-view .single-selects-editor-list .single-select-container:hover,
.multiple-select-view .multiple-selects-editor-list .multiple-select-container:hover {
  background-color: #fff;
}

.single-select-view .single-selects-editor-list .add-search-result,
.multiple-select-view  .multiple-selects-editor-list .add-search-result {
  height: 50px;
}

.edit-option-view .options-container {
  background-color: #fff;
  padding-left: 1rem;
  border-bottom: 1px solid #e9e9e9;
}

.edit-option-view .options-container .colorinput {
  height: 28px;
}

.edit-option-view .options-container .single-select-check-icon {
  line-height: 27px;
  color: #202428 !important;
}

.edit-option-view .option-item {
  padding: 0.626rem 1rem 0.625rem 0;
  border-bottom: 1px solid #e9e9e9;
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.edit-option-view .option-item:last-child {
  border-bottom: none;
}

.edit-option-view .form-control {
  border: none;
  height: 50px;
  padding: 0 1rem;
  border-top: 1px solid #e9e9e9;
}

.row-expand-view .links-editor-list,
.row-expand-view .collaborators-editor-list {
  padding: 0;
  width: 100%;
  border-top: 1px solid #e9e9e9;
  background-color: #fff;
  min-height: auto;
}

.row-expand-view .links-editor-list .links-container,
.collaborator-editor-view .collaborators-editor-list .collaborators-container {
  overflow: visible;
  max-height: none;
  padding: 0 0 0 1rem;
}

.row-expand-view .links-editor-list .links-container .link-item,
.collaborator-editor-view .collaborators-editor-list .collaborators-container .collaborator-item {
  height: 50px;
  border-bottom: 1px solid #e9e9e9;
  padding-right: 1rem; 
}

.collaborator-editor-view .collaborators-editor-list .collaborators-container .collaborator-avatar {
  height: 24px;
  width: 24px;
  line-height: 24px;
  margin-left: 0;
}

.collaborator-editor-view .collaborators-editor-list .collaborators-container .collaborator-name {
  margin-left: 10px;
  line-height: 24px;
}

.collaborator-editor-view .collaborators-editor-list .collaborator-container:hover {
  background-color: #fff;
}

.links-editor-list .search-links,
.collaborators-editor-list .search-collaborators {
  padding: 0.626rem 1rem;
  height: 50px;
}

.am-list-body .row-expand-collaborators .collaborator {
  margin-bottom: 5px;
}

.view-partition {
  background-color: #f5f5f5;
  height: 20px;
}

.view-partition-border-top {
  border-top: 1px solid #e9e9e9;
}

.view-partition-border-bottom {
  border-bottom: 1px solid #e9e9e9;
}

.view-subtitle {
  padding: 1rem 1rem 0.3125rem;
  background-color: #f5f5f5;
  border-top: 1px solid #e9e9e9;
  border-bottom: 1px solid #e9e9e9;
}

.row-expand-view .long-text-container p a {
  word-break: break-all;
}

.long-text-view .long-text-container {
  height: calc(100% - 71px);
  overflow-y: auto;
}

.long-text-view .long-text-container .am-list-item {
  height: unset;
}

.long-text-view .long-text-container .am-textarea-item {
  padding: 0 1rem;
  border-bottom: 1px solid #e9e9e9;
}

.long-text-view .long-text-container .am-textarea-item textarea {
  font-size: 14px;
  overflow-x: hidden;
}

.date-editor-view {
  background-color: #f5f5f5;
}

.date-editor-view .date-input {
  background-color: #fff;
  display: inline-block;
}

.date-editor-view .date-input .date-input-day {
  background-color: #f5f5f5;
  border: 1px solid #e9e9e9;
  margin: 10px;
  padding: 3px 5px;
  border-radius: 3px;
}

.row-expand-view-footer .clear-date {
  background-color: #fff;
  text-align: center;
  padding: 10px 0;
  color: #d27053;
  border-bottom: 1px solid #e9e9e9;
  border-top: 1px solid #e9e9e9;
}

.row-expand-view .rc-calendar {
  box-shadow: none;
  border-radius: 0px;
  border: 1px solid #e9e9e9;
  border-left: none;
  border-right: none;
}

.row-expand-view .rc-calendar-body {
  height: 22rem;
}

.row-expand-view .rc-calendar-date-panel {
  width: 100%;
}

.row-expand-view .rc-calendar-table tr {
  height: 3rem;
}

.row-expand-view .rc-calendar-year-select,
.row-expand-view .rc-calendar-month-select,
.row-expand-view .rc-calendar-day-select {
  font-size: 16px;
  padding: 5px;
}

.row-expand-view .rc-calendar-prev-month-btn,
.row-expand-view .rc-calendar-next-month-btn,
.row-expand-view .rc-calendar-prev-year-btn,
.row-expand-view .rc-calendar-next-year-btn {
  font-size: 30px;
  width: 50px;
}

.row-expand-view .rc-calendar-header {
  border-bottom: none;
  margin-top: 0.5rem;
}

.row-expand-view .rc-calendar-header .rc-calendar-next-month-btn {
  right: 50px;
}

.row-expand-view .rc-calendar-header .rc-calendar-prev-month-btn {
  left: 50px;
}

.row-expand-view .rc-calendar-date {
  width: 36px;
  height: 36px;
  text-align: center;
  line-height: 36px;
}

.row-expand-view .row-expand-item-file,
.row-expand-view .row-expand-item-img {
  height: auto;
  width: calc(50% - 10px);
}

.row-expand-view .row-expand-item-img,
.row-expand-view .row-expand-item-file {
  margin-left: 0px;
  margin-right: 0px;
}

.row-expand-view .row-expand-item-img img,
.row-expand-view .row-expand-item-file img {
  height: unset;
  width: unset;
}

.row-expand-view .row-expand-item-file:nth-child(even),
.row-expand-view .row-expand-item-img:nth-child(even) {
  margin-left: 20px;
}

.row-expand-view .row-expand-item-file img,
.row-expand-view .row-expand-item-img img {
  max-height: 100%;
  max-width: 100%;
  border-radius: 2px;
}

.row-expand-view .row-expand-container-img, .row-expand-view .row-expand-container-file {
  margin: 1rem 0 0 0;
}

.image-editor-view {
  background-color: #f5f5f5;
}

.image-editor-view .image-previewer-wrapper {
  height: auto;
}

.image-editor-view .image-previewer-wrapper .image-previewer-content {
  width: auto;
  margin-bottom: 15px;
}

.image-editor-view .image-editor-container .image-previewer-container {
  border-bottom: 1px solid #e9e9e9;
}

.image-editor-view .file-previewer-wrapper {
  height: unset;
  overflow: visible;
}

.image-editor-view .file-previewer-box {
  height: 64px;
  width: calc(100% - 16px);
  border-bottom: 1px solid #e9e9e9;
  padding: 0.5rem 1rem 0.5rem 0;
  margin-left: 16px;
}

.image-editor-view .file-previewer-box:last-child {
  border-bottom: none;
}

.image-editor-view .file-previewer-operation {
  color: #888;
}

.image-editor-view .mobile-add-btn {
  margin-top: 0;
}

.row-expand-view .number-container {
  height: 50px;
  line-height: 50px;
  font-family: sans-serif;
}

.row-expand-view .number-container .form-control {
  height: inherit;
  border: none;
  padding: 0;
  margin: 0;
  font-size: 14px;
  color: #212529;
}

.row-expand-view .number-container .form-control:focus {
  box-shadow: none;
}

.row-expand-view .mobile-checkbox-editor-container .show-box {
  margin: 18px;
}

@media screen and (max-width: 767.8px) {
  .row-expand-view .row-expand-item-img:nth-child(even) {
    margin-left: 20px;
  }

  .image-editor-view .image-previewer-wrapper .image-previewer-box {
    margin-right: 0;
  }

  .image-editor-view .image-previewer-wrapper .image-previewer-box:nth-child(even) {
    margin-left: 15px;
  }
}

@media screen and (min-width: 767.8px) {
  .row-expand-view .row-expand-item-img {
    margin-left: 20px;
  }

  .row-expand-view .row-expand-item-img:nth-child(3n + 1) {
    margin-left: 0px;
  }

  .image-editor-view .image-previewer-wrapper .image-previewer-box {
    margin-left: 0px;
  }

  .image-editor-view .image-previewer-wrapper .image-previewer-box:nth-child(3n) {
    margin-right: 0px;
  }
}

.am-list-body .row-expand-item .mobile-multiple-select {
  min-height: 30px;
  margin: 10px 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.am-list-body .multiple-select-item .multiple-select-option {
  max-width: 150px;
  margin: 0 10px 0 0;
}

.row-expand-item .mobile-multiple-select .multiple-select-item {
  margin: 5px 0;
  height: 20px;
  flex-shrink: 0;
}

.row-expand-item .mobile-multiple-select .remove-container {
  margin-left: 5px;
}

.row-expand-view .links-editor-list .links-container .link-item:last-child {
  border-bottom: none;
}

.row-expand-view .links-editor-list .links-container {
  border-bottom: 1px solid #e9e9e9;
}

.row-expand-view .form-control:disabled, .row-expand-view .form-control[readonly] {
  background-color: #fff;
}

.row-expand-view .geolocation-editor-container {
  box-shadow: none;
  width: 100%;
}

.row-expand-view .geolocation-editor-detail-info,
.row-expand-view .geolocation-editor-selector {
  width: 200px;
}

.row-expand-view .geolocation-editor-footer {
  border-top: none;
}