.file-editor-modal {
  width: 560px;
}

.file-editor-modal .dtable-icon-return {
  color: #777777;
  font-size: 14px;
}

.user-select-none {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -khtml-user-select: none;
  user-select: none;
}

.file-formatter {
  padding: 2px 0;
  display: inline-block;
}

.file-formatter img {
  max-height: 28px;
  margin-right: 4px;
  border: 1px solid #f0f0f0;
}

.file-formatter img:hover {
  border: 1px solid #c9c9c9;
}

.file-editor-container {
  min-height: 300px;
}

.file-addition-container {
  display: flex;
  flex-direction: row;
}

.file-editor-title {
  font-size: 16px;
  color: #000;
  margin-left: 4px;
}

.file-addition-left {
  border-right: 1px solid #e9ecef;
  width: 142px;
  padding: 16px 0 10px 16px;
}

.file-addition-left .file-addition-nav {
  padding-right: 10px;
}

.file-addition-left .addition-item {
  padding: 5px 0 5px 12px;
  border-radius: 3px;
  display: inline-block;
  cursor: pointer;
  width: 100%;
}

.file-addition-left .addition-item:hover {
  background-color: #feefb8;
}

.file-addition-container .file-addition-left .selected-additoin-item {
  background-color: #f3af7d;
  color: #fff;
}

.file-addition-right {
  height: 350px;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 16px 16px;
  width: 416px;
}

.file-addition-right .file-addition-right-container {
  display: flex;
  flex-wrap: wrap;
  width: 384px;
  height: 100%;
}

.file-addition-right .file-wrapper {
  height: 120px;
  width: 120px;
  border: 1px solid #ededed;
  border-radius: 4px; 
  position: relative;
  margin-right: 12px;
  margin-bottom: 12px;
}

.file-addition-right .file-wrapper:nth-child(3n) {
  margin-right: 0px;
}

.file-addition-right .file-wrapper .file-add-box {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  flex-direction: column;
  font-size: 12px;
  cursor: pointer;
}

.file-addition-right .file-wrapper:hover .file-add-button i {
  color: orange;
}

.file-addition-right .file-wrapper .file-add-button {
  position: relative;
  cursor: pointer;
}

.file-addition-right .file-wrapper .file-add-button i {
  color: #ccc;
  font-size: 50px;
}

.file-addition-right .file-wrapper .file-wrapper-drop-active i {
  color: orange;
}

.file-addition-right .file-wrapper .file-add-span {
  margin-top: 5px;
  font-size: 12px;
}

.file-addition-right .file-wrapper .file-content {
  width: 118px;
  height: 118px;
  text-align: center;
  line-height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.file-addition-right .file-wrapper .delete-file-icon {
  position: absolute;
  top: -4px;
  right: -4px;
  height: 32px;
  width: 32px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  text-align: center;
  cursor: pointer;
  transform: scale(0.5);
}

.file-addition-right .file-wrapper .delete-file-icon .file-delete-span {
  font-size: 20px;
}

.file-addition-right .file-upload-container {
  height: 100%;
  width: 100%;
  border: 1px dashed transparent;
  position: relative;
  cursor: pointer;
}

.file-addition-right .file-upload-container:hover .dtable-icon-add-files {
  color: orange;
}

.file-addition-right .file-upload-container-active {
  border: 1px dashed #ccc;
}

.file-addition-right .file-upload-container .file-drop-active .dtable-font {
  color: orange;
}

.file-addition-right .file-upload-container .file-tip-addition {
  text-align: center;
  padding: 6rem 4rem;
  width: 100%;
}

.file-tip-addition .file-add-icon {
  font-size: 54px;
  line-height: 1;
  position: relative;
  height: 54px;
  width: 54px;
  margin: 0 auto;
}

.file-tip-addition .file-add-icon input {
  opacity: 0;
  width: 100%;
  height: 54px;  
  position: absolute;
  left: 0;
  top: 0;
}

.file-tip-addition .file-add-icon i {
  font-size: 54px;
  color: #ccc;
}

.file-addition-right .file-upload-container .file-add-span {
  margin-top: 5px;
  font-size: 12px;
}

.file-addition-right .file-upload-container .file-add-span .file-add-span-btn {
  position: relative;
  color: orange;
  cursor: pointer;
}

.file-previewer-container {
  position: relative;
}

.file-previewer-wrapper {
  height: 350px;
  overflow-y: auto;
}

.file-previewer-content {
  margin-top: 10px;
}

.file-previewer-box {
  display: flex;
  height: 48px;
  width: 100%;
  padding: 0px 16px;
  justify-content: space-between;
}

.file-previewer-box-hignlight {
  background-color: #f5f5f5;
}

.file-previewer-item {
  display: flex;
  width: 90%;
}

.file-previewer-icon {
  height: 100%;
  line-height: 48px;
  margin-right: 5px;
}

.file-previewer-icon img {
  width: 36px;
  height: 36px;
}

.file-previewer-info {
  width: 90%;
  font-size: 14px;
  margin-top: 5px;
}

.file-previewer-item-name {
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}

.file-previewer-item-name-height {
  height: 100%;
  display: flex;
  align-items: center;
}

.file-previewer-item-name span:hover {
  text-decoration: underline;
  cursor: pointer;
}

.file-previewer-item-size {
  color: #c0c0c0;
}

.file-upload-success {
  width: 60px;
  height: 60px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 2px;
  position: absolute;
  z-index: 5;
}

.file-upload-success .file-upload-success-scale {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.file-upload-success .file-upload-success-scale .file-upload-success-tip {
  color: #fff;
  font-size: 12px;
  width: 100%;
  transform: scale(0.8);
  text-align: center;
}

.file-upload-success .file-upload-success-icon i {
  display: inline-block;
  color: #757474;
}

.file-upload-mask {
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 2;
  border-radius: 2px;
}

.file-upload-error-tip {
  z-index: 3;
  font-size: 12px;
  transform: scale(0.7);
  position: absolute;
  color: #fff;
  white-space: nowrap;
  text-align: center;
}

.file-upload-again {
  text-decoration: underline;
  cursor: pointer;
  margin-left: 3px;
}

.file-upload-percent {
  width: 100%;
  height: 100%;
  border-radius: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.file-upload-span {
  transform: rotate(90deg) scale(0.5);
  color: #fff !important;
}

.upload-file {
  width: 1px;
  height: 1px;
  opacity: 0;
  position: absolute;
  top: 0;
  font-size: 0;
  cursor: pointer;
}

/* itemdropdown */

.file-previewer-operation {
  display: flex;
  justify-content: center;
  align-items: center;
}

.file-previewer-operation .file-download-icon {
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #949494;
  cursor: pointer;
}

.file-previewer-operation .file-download-icon:hover{
  color: #000;
  opacity: 0.75;
}

.item-dropdown {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 24px;
  width: 24px
}

.item-dropdown-more {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  cursor: pointer;
}

.item-dropdown .dtable-icon-more-vertical {
  font-size: 14px;
  color: #949494;
}

.item-dropdown .dtable-icon-more-vertical:hover{
  color: #000;
  opacity: 0.75;
}

.item-dropdown-menu .dropdown-item:hover {
  background-color: #ededed;
  color: #6e7687;
  cursor: pointer;
}

/* rename */
.rename-container button {
  margin-left: 0.25rem;
  padding: 5px 6px;
  font-size: 1rem;
  line-height: 1;
  color: #666;
  min-width: 0;
  margin-top: -4px;
}

.rename-container .dtable-font {
  font-size: 12px;
  color: #ccc;
}

.rename-container input {
  padding: 2px 3px;
  width: 160px;
  height: 24px;
  line-height: 19px;
  border-radius: 2px;
  word-wrap: break-word;
  border: 1px solid #ccc;
  font-size: 14px;
}

.rename-container input:focus {
  color: #495057;
  background-color: rgba(230, 240, 255);
  border-color: #6db5ec;
  outline: 0;
  border: 1px solid rgb(124, 175, 253);
}