.share-dialog {
  max-width: 720px;
}

.share-dialog .share-dialog-content {
  padding: 0;
  min-height: 27rem;
  display: flex;
  flex-direction: row;
  overflow: hidden;
}

.share-dialog-content .share-dialog-side {
  display: flex;
  flex: 0 0 22%;
  padding: 1rem 0 0 1rem;
  border-right: 1px solid #eee;
}

.share-dialog-side .nav-pills .nav-item .nav-link {
  width: 100%;
}

.share-dialog-content .share-dialog-main {
  display: flex;
  flex: 0 0 78%;
  padding: 1rem 1.5rem 2rem;
}
.share-dialog-content .share-dialog-main .tab-content {
  flex: 1;
}
.share-dialog-content .share-dialog-main .tab-pane {
  height: 100%;
}

.share-list-container {
  min-height: 14rem;
  max-height: 18rem;
  overflow: auto;
  height: 100%;
}

.share-dialog-content label {
  padding: 0.5rem 0 0.25rem;
}

.share-dialog-content label.form-check-label {
  padding: 0.25rem 0;
}

.link-operation-content {
  margin-left: 1.25rem;
}

.link-operation-icon {
  color: #9aa0ac;
}

.tip {
  color: #808080;
  margin-bottom: 1rem;
}

.generate-share-link .passwd,
.generate-upload-link .passwd {
  width: 60%;
}

.generate-share-link .permission {
  margin-left: 2.5rem;
}

.generate-link-btn {
  margin-top: 1.125rem;
}

.share-dialog .nav .nav-item .nav-link {
  padding: 0.3125rem 1rem 0.3125rem 0.25rem;
}

input.expire-input {
  display: inline-block;
  width: 4rem;
  height: 1.5rem;
  padding: 0.25rem 0.25rem;
  margin: 0 0.25rem 0 1.25rem;
}

.expire-input-border {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  height: 2.375rem !important;
  padding: 0.375rem 0.75rem !important;
}

.share-dialog-main .permission-editor .permission-editor__menu {
  width: 240px;
}

.expir-span {
  border: 1px solid rgba(0, 40, 100, 0.12);
  border-left: none;
  font-size: 0.9375rem;
  margin-left: -5px;
  padding: 0px 8px;
  height: 2.375rem;
  display: inline-block;
  text-align: center;
  position: absolute;
  top: 4px;
  min-width: 2.375rem;
  line-height: 2.25;
  border-bottom-right-radius: 3px;
  border-top-right-radius: 3px;
}

.dtable-share-link-list-container {
  overflow: auto;
  margin-top: 1.25rem;
  height: calc(100% - 91px);
}

.share-link-container .share-link-tip,
.external-link-container .external-link-tip {
  background-color: #f6eddf;
  margin: -1rem -1.5rem 1rem;
  padding: 0.5rem 1rem;
}

.column-flex-direction {
  display: flex;
  flex-direction: column;
}

.share-dialog .action-icon {
  margin-left: 0.5rem;
  font-size: 1.25rem;
  font-style: normal;
  line-height: 1;
  cursor: pointer;
  vertical-align: middle;
  color: #888 !important;
}

.share-dialog .action-icon:focus,
.share-dialog .action-icon:hover {
  color: #333 !important;
  text-decoration: none;
}

.share-dialog .action-icon.hide {
  display: none;
}

.share-dialog .nav .nav-item {
  padding: 0;
}
.share-dialog .nav .nav-item .nav-link {
  padding: 0.5rem 0;
  margin-right: 0.5rem;
  color: #8a948f;
  font-weight: normal;
  transition: none;
}

.share-dialog .nav .nav-item .nav-link.active {
  color: #eb8205;
  text-decoration: none;
  border-bottom: 0.125rem solid #eb8205;
}

.share-dialog .nav-pills .nav-item .nav-link {
  padding: .3125rem 1rem .3125rem .25rem;
  color: #333;
}

.share-dialog .nav-pills .nav-item .nav-link:hover {
  background-color: #feefb8;
}

.share-dialog .nav-pills .nav-item .nav-link.active {
  background-color: #feac74;
  color: #fff;
  border: none;
}

.share-dialog table {
  width: 100%;
  table-layout: fixed;
}

.share-dialog table thead tr {
  height: 2.1875rem;
}

.share-dialog table tbody tr {
  height: 2.5625rem;
}

.share-dialog table th {
  padding: 0.3125rem 0.1875rem;
  border-bottom: 1px solid #eee;
  text-align: left;
  font-weight: normal;
  font-size: 0.8125rem;
  line-height: 1.6;
  color: #9c9c9c;
}

.share-dialog table td {
  padding: 0.5rem 0.1875rem;
  border-bottom: 1px solid #eee;
  color: #333;
  font-size: 0.875rem;
  word-break: break-all;
}

.share-dialog .table-thead-hidden thead tr {
  height: 0;
  border: 0;
}

.share-dialog .table-thead-hidden thead th {
  padding: 0;
  border: 0;
  font-size: 0;
}

.share-dialog .tr-highlight {
  background-color: #f8f8f8;
}

.share-dialog .tr-active {
  background-color: #f2f4f6 !important;
}

.share-dialog .ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.share-dialog .select-module {
	font-size: 1rem;
}
.share-dialog .select-module.select-module-icon {
	width: 1.5rem;
	height: 1.5rem;
}

.share-dialog .select-module.select-module-name {
	margin-left: 0.5rem;
}

.share-dialog .op-target {
  color: #ee8204;
  word-wrap: break-word;
}
