.formula-container {
  height: 374px;
  background-color: #f2f2f2;
  margin: 0 -1rem -1rem -1rem;
  padding: 0.8rem 1rem .5rem 1rem;
}

.formula-container .formula-content {
  height: 316px; 
  border-radius: 4px;
  background: #fff;
}

.formula-container .formula-input {
  height: 38px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.formula-container .formula-tips {
  height: 278px;
  width: 100%;
  border: 1px solid rgba(0, 40, 100, 0.12);
  border-top: 0;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  display: flex;
  justify-content: center;
}

.formula-container .formula-column-func {
  width: 150px;
  height: 100%;
  border-right: 1px solid rgba(0, 40, 100, 0.12);
  overflow-y: scroll;
}

.formula-container .formula-column-func::-webkit-scrollbar {
  width: 0;
}

.formula-container .table-columns {
  border-bottom: 1px solid rgba(0, 40, 100, 0.12);
}

.formula-container .table-columns .formula-column-title {
  height: 30px;
  padding: 0 10px 0 12px;
  color: #999;
  font-size: 12px;
  line-height: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  -webkit-user-select: none;  
  -moz-user-select: none;  
  -o-user-select: none;  
  -ms-user-select: none;
}

.formula-container .formula-column-title .dtable-font {
  color: #ccc;
  font-size: 10px;
}

.formula-container .formula-column-title .dtable-icon-spin {
  transform: rotate(90deg);
}

.formula-container .table-columns .formula-column-title:hover {
  cursor: pointer;
}

.formula-container .table-columns .formula-tip-column {
  padding: 0 4px 0 12px;
  height: 28px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.formula-container .table-columns .formula-tip-column:hover {
  background-color: #f2f2f2;
  cursor: pointer;
}

.formula-tip-column .formula-name {
  width: calc(100% - 24px);
  height: 100%;
  line-height: 28px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 13px;
  color: #000;
  -webkit-user-select: none;  
  -moz-user-select: none;  
  -o-user-select: none;  
  -ms-user-select: none;
}

.formula-container .formula-tip-column:hover .set-formula {
  display: block;
}

.formula-tip-column .set-formula {
  display: none;
  height: 24px;
  width: 24px;
  line-height: 24px;
  text-align: center;
}

.formula-tip-column .set-formula:hover .set-formula-icon {
  color: #888;
}

.formula-tip-column .set-formula .set-formula-icon {
  font-size: 12px;
  color: #aaa;
  font-weight: 500;
}

.formula-container .formula-tip-column .formula-name-icon {
  font-size: 13px;
  margin-right: 10px;
  color: #999;
}

.formula-container .formula-example-container {
  width: 370px;
  height: 100%;
  padding: 0 10px;
  overflow-y: scroll;
}

.formula-container .formula-example-container::-webkit-scrollbar {
  width: 0;
}

.formula-example-container .formula-example-content {
  height: auto;
  width: 100%;
}

.formula-example-container .formula-example-content:last-child {
  margin-bottom: 10px;
}

.formula-example-container .formula-example-content .formula-example-title {
  font-weight: 500;
  color: #333;
  font-size: 15px;
  line-height: 32px;
}

.formula-example-container .formula-example-content .formula-example-description {
  color: #333;
  font-size: 13px;
}

.formula-example-container .formula-example-content .formula-example-title-tip {
  font-weight: 600;
  color: #333;
  font-size: 13px;
  line-height: 32px;
}

.formula-example-container .formula-example-content .formula-example-message {
  background-color: #f7f7f7;
  font-size: 13px;
  border-radius: 3px;
  line-height: 30px;
  padding: 0 5px;
  display: flex;
  flex-direction: column;
}