.mobile-sorts-view, .mobile-options-box {
  min-height: 100%;
  width: 100%;
  background-color: #F3F3F3;
  position: fixed;
  left: 0;
  top: 0;
}

.mobile-sorts-view .mobile-sorts-header {
  border-bottom: 1px solid #e9e9e9;
}

.mobile-sorts-view .mobile-sorts-list {
  width: 100%;
  overflow-y: scroll;
}

.mobile-sorts-view .mobile-sorts-list::-webkit-scrollbar {
  width: 0;
}

.mobile-sorts-list .am-list-body {
  border-bottom: 1px solid #e9e9e9 !important;
}

.mobile-sorts-view .am-list-item {
  padding-left: 16px;
  height: 50px;
}

.mobile-sorts-view .delete-sort {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid #e9e9e9;
  padding: 0 0 0 16px;
  color: #e94f4f;
  background: #fff;
  line-height: 50px;
}

.mobile-sorts-list .am-list-line .am-list-content {
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.am-list-content .select-option-name {
  color: #202428;
  font-size: 14px;
}

.mobile-sorts-list .am-list-content .dtable-icon-right {
  color: #999;
}