.mobile-statistics-view, .mobile-options-box {
  min-height: 100%;
  width: 100%;
  background-color: #F3F3F3;
  position: fixed;
  left: 0;
  top: 0;
}

.mobile-statistics-view .mobile-statistics-header {
  border-bottom: 1px solid #e9e9e9;
}

.mobile-statistics-view .mobile-statistic-toolbar i {
  color: #999;
}

.mobile-statistics-view .mobile-statistics-container {
  width: 100%;
  overflow-y: scroll;
}

.mobile-statistics-view .mobile-statistics-list {
  width: 100%;
  padding: 15px 10px 0 10px;
  box-sizing: border-box;
}

.mobile-statistics-view .mobile-statistics-container::-webkit-scrollbar {
  width: 0;
}

.mobile-statistics-view .mobile-add-btn {
  margin: 0;
}

.mobile-statistics-view .mobile-statistic {
  min-height: 200px;
  border: 1px solid #e9e9e9;
  box-sizing: border-box;
  background: #fff;
  margin-bottom: 10px;
  border-radius: 4px;
}

.mobile-statistics-view .mobile-statistic .mobile-statistic-header {
  height: 50px;
  width: 100%;
  padding: 0 10px;
  border-bottom: 1px solid #e9e9e9;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.mobile-statistic-container {
  padding: 8px;
  max-height: 300px;
  overflow-y: scroll;
}

.mobile-statistic-container::-webkit-scrollbar {
  width: 0;
}

.mobile-statistic-container .mobile-statistic-table-wrapper {
  margin: 15px auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mobile-statistic-container .mobile-statistic-table-wrapper table {
  width: 90%;
  table-layout: fixed;
}

.mobile-statistic-container .mobile-statistic-table-wrapper table thead {
  border-top: 1px solid #ccc;
}

.mobile-statistic-container .mobile-statistic-table-wrapper td, .mobile-statistic-container .mobile-statistic-table-wrapper th {
  padding: 10px;
  border-right: 1px solid #ccc;
  border-left: 1px solid #ccc;
  overflow: hidden;
  word-break: keep-all;
  text-overflow: ellipsis;
}

.mobile-statistic-container .mobile-statistic-table-wrapper tr {
  border-bottom: 1px solid #ccc;
  text-align: center;
}

.mobile-chart-container div canvas {
  width: 100% !important;
}

.mobile-statistic-container .g2-guide-html {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.mobile-statistic-container .g2-guide-html .title {
  font-size: 14px;
  margin-bottom: 0;
}

.mobile-statistic-container .g2-guide-html .value.sum-value {
  font-size: 25px;
  font-weight: 400;
}
