.hide-columns-dropdown-menu {
  width: 180px;
  min-width: unset;
  min-height: 160px;
  font-size: 12px;
  color: #555;
}

.hide-columns-dropdown-menu .search-column {
  height: 28px;
  padding: 0 10px;
}

.hide-columns-dropdown-menu .search-column input {
  width: 100%;
  height: 28px;
}

.hide-columns-dropdown-menu .columns-list {
  max-height: 500px;
  padding: .5rem 0;
  overflow: auto;
}

.hide-columns-dropdown-menu .dropdown-item {
  display: flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
}

.hide-columns-dropdown-menu .dropdown-item:hover {
  background: #f5f5f5;
}

.hide-columns-dropdown-menu .dropdown-item:active {
  background: #f5f5f5;
}

.hide-columns-dropdown-menu .column-can-drop {
  background: #e9e9e9;
}

.hide-columns-dropdown-menu .drag-column-handle {
  display: flex;
}

.hide-columns-dropdown-menu .drag-column-handle:hover {
  cursor: move;
  cursor: grab;
  cursor: -webkit-grab;
}

.hide-columns-dropdown-menu .drag-column-handle .dtable-font {
  font-size: 14px;
  color: #c2c2c2;
}

.hide-columns-dropdown-menu .is-hide-column {
  display: flex;
  flex: 1;
  padding-left: 10px;
}

.hide-columns-dropdown-menu .is-hide-column:hover {
  cursor: pointer;
}

.hide-columns-dropdown-menu .is-hide-column .custom-switch {
  width: 100%;
  justify-content: space-between;
  cursor: pointer;
}

.hide-columns-dropdown-menu .is-hide-column .custom-switch-description {
  width: 92px;
  margin: 0;
  padding-right: 5px;
}

.hide-columns-dropdown-menu .custom-switch-description .dtable-font {
  font-size: 12px;
  margin-right: 10px;
}

.hide-columns-dropdown-menu .is-hide-column .custom-switch-indicator {
  width: 22px;
  height: 12px;
  border-radius: 6px;
}

.hide-columns-dropdown-menu .is-hide-column .custom-switch-indicator::before {
  height: 8px;
  width: 8px;
}

.hide-columns-dropdown-menu .custom-switch-input:checked ~ .custom-switch-indicator:before {
  left: 12px;
}

.hide-columns-dropdown-menu .operation-all {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0 17px;
}

.hide-columns-dropdown-menu .operation-all .hide-all,
.hide-columns-dropdown-menu .operation-all .show-all {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 28px;
  width: 64px;
  background: #f5f5f5;
  color: #999;
  border-radius: 3px;
}

.hide-columns-dropdown-menu .operation-all .hide-all:hover,
.hide-columns-dropdown-menu .operation-all .show-all:hover {
  background: #e9e9e9;
  color: #333;
  cursor: pointer;
}

.hide-columns-dropdown-menu .none-columns,
.hide-columns-dropdown-menu .none-search-result {
  height: calc(104px - 1rem);
  padding: 10px;
  font-size: 14px;
  opacity: .5;
  display: inline-block;
}

.hidden-column-view {
  height: 100%;
  width: 100%;
  background-color: #fff;
  position: absolute;
  top: 0;
}

.hidden-column-view .am-list-header {
  border-bottom: 1px solid #e9e9e9;
  background-color: #fff;
  padding: 0;
}

.hidden-column-view .hide-columns-dropdown-menu {
  top: 71px;
  width: 100%;
  box-shadow: none;
  border: 0;
  font-size: 14px;
  padding-bottom: 0;
  margin-top: 0px;
  height: calc(100% - 71px);
  overflow-y: auto;
}

.hidden-column-view .hide-columns-dropdown-menu::-webkit-scrollbar {
  width: 0;
}

.hidden-column-view .hide-columns-dropdown-menu .search-column {
  margin-bottom: 10px;
}

.hidden-column-view .hide-columns-dropdown-menu .sub-title {
  background-color: #f5f5f5;
  justify-content: space-between;
  padding: 1rem 1rem 0.3125rem;
  border-bottom: 1px solid #e9e9e9;
  border-top: 1px solid #e9e9e9;
}

.hidden-column-view .hide-columns-dropdown-menu .none-search-result {
  padding: 1rem;
}

.hidden-column-view .hide-columns-dropdown-menu .search-column {
  height: 32px;
}

.hidden-column-view .hide-columns-dropdown-menu .search-column input {
  border: none;
  box-shadow: none;
}

.hidden-column-view .hide-columns-dropdown-menu .search-column input:focus {
  border-color: rgba(0, 40, 100, 0.12);
  box-shadow: none;
}

.hidden-column-view .hide-columns-dropdown-menu .columns-list {
  padding: 0;
  max-height: unset;
}

.hidden-column-view .hide-columns-dropdown-menu .dropdown-item {
  padding: 1rem 1rem 1rem 0;
  margin-left: 1rem;
  border-bottom: 1px solid #e9e9e9;
  width: auto;
}

.hidden-column-view .hide-columns-dropdown-menu .dropdown-item .is-hide-column {
  padding-left: 0px;
}

.hidden-column-view .hide-columns-dropdown-menu .custom-switch-description .dtable-font {
  font-size: 14px;
  color: #666;
}

.hidden-column-view .hide-columns-dropdown-menu .is-hide-column .custom-switch-indicator {
  width: 2.25rem;
  height: 1.25rem;
  border-radius: 50px;
}

.hidden-column-view .hide-columns-dropdown-menu .custom-switch-input:checked ~ .custom-switch-indicator:before {
  left: calc(1rem + 1px);
}

.hidden-column-view .hide-columns-dropdown-menu .is-hide-column .custom-switch-indicator::before {
  width: calc(1.25rem - 4px);
  height: calc(1.25rem - 4px);
}

.hidden-column-view .hide-columns-dropdown-menu .is-hide-column .custom-switch-description {
  width: calc(100% - 40px);
}

.hidden-column-view .custom-switch-description {
  color: #212529;
}
