.mobile-edit-statistic-view {
  min-height: 100%;
  width: 100%;
  background-color: #F3F3F3;
  position: fixed;
  left: 0;
  top: 0;
}

.mobile-edit-statistic-view .mobile-edit-statistic-header {
  border-bottom: 1px solid #e9e9e9;
}

.mobile-edit-statistic-container {
  overflow-y: scroll;
}

.mobile-edit-statistic-container::-webkit-scrollbar {
  width: 0;
}

.mobile-edit-statistic-container .mobile-list-header .am-list-body {
  border-bottom: 1px solid #e9e9e9;
}

.mobile-edit-statistic-container .am-list-body .am-list-content {
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #202428;
  font-size: 14px;
}

.mobile-edit-statistic-container .am-list-content .edit-statistic-type {
  flex-shrink: 0;
  display: inline-block;
  max-width: 140px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.mobile-edit-statistic-container .am-list-content .current-value-container {
  flex-shrink: 0;
  max-width: 140px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.am-list-content .current-value-container .current-value, .am-list-content .current-value-container .edit-axis-column {
  display: block !important;
  padding: 0 8px 0 0;
  max-width: 126px;
  box-sizing: border-box;
  color: #999;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.current-value-container .edit-axis-column .header-icon {
  flex-shrink: 0;
}

.am-list-content .current-value-container .select-option-name {
  text-align: right;
  color: #999;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.mobile-edit-statistic-container .am-list-content .dtable-icon-right {
  color: #bcbcbc;
}

.mobile-edit-statistic-container .mobile-statistic-name {
  height: 50px;
}

.mobile-edit-statistic-container .mobile-statistic-name .form-control {
  padding: 6px 16px;
  height: 100%;
  width: 100%;
  border: none;
  box-sizing: border-box;
}

.mobile-edit-statistic-container .mobile-statistic-name .form-control:focus {
  border-color: rgba(0, 40, 100, 0.12);
  box-shadow: none;
}

.mobile-edit-statistic-view .am-list-item {
  padding-left: 16px;
  height: 50px;
}

.mobile-edit-statistic-view .am-list-item i {
  font-size: 14px;
  color: #aaa;
}

.mobile-custom-switch-container .custom-switch {
  width: 100%;
  justify-content: space-between;
}

.mobile-custom-switch-container .custom-switch .custom-switch-indicator {
  margin-right: 3px;
}

.mobile-custom-switch-container .custom-switch .custom-switch-description {
  margin-left: 0;
}

.x-axis-settings .mobile-custom-switch-container .custom-switch-description {
  color: #000;
}