.sorts-list {
  min-height: 120px;
  max-height: 100%;
  padding: 15px;
}

.sorts-list .sort-item {
  display: flex;
  align-items: center;
  padding: .25rem 0;
}

.sort-item .condition {
  display: flex;
  flex: 1 1;
}

.sort-item .condition>div {
  height: 38px;
  line-height: 38px;
  margin-left: .5rem;
}

.sort-item .sort-column {
  width: 150px;
}

.sort-item .sort-predicate {
  width: 100px;
}  

.sort-list .delete-sort {
  width: 12px;
  height: 20px;
  margin-left: 1rem;
  text-align: center;
}

.sorts-list .empty-sorts-list {
  color: #949494;
}

.delete-sort .dtable-icon-fork-number {
  display: inline-block;
  font-size: 12px;
  color: #949494;
  cursor: pointer;
}