.statistic-table-records-dialog {
  max-width: 1100px;
  width: 1100px;
  height: calc(100% - 56px);
}

.statistic-table-records-dialog .modal-content {
  height: 100%;
}

.statistic-table-records-container {
  width: 100%;
  height: calc(100% - 57px);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}

.statistic-table-records-container .statistic-table-view {
  height: 100%;
  overflow: scroll;
  flex-shrink: 0;
}

.statistic-table-records-container .statistic-table-rows {
  width: 30%;
  height: 100%;
  flex-shrink: 0;
  padding: 2px 0 10px;
  background-color: #f8f8f8;
}

.statistic-table-rows .search-rows {
  height: calc(100% - 40px);
  width: 100%;
}

.statistic-table-rows .search-rows .records-list {
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  padding: 10px 10px 0;
}

.statistic-table-rows .search-rows .records-container {
  height: auto;
  width: 100%;
}

.statistic-table-rows .search-rows::-webkit-scrollbar {
  width: 0;
}

.statistic-table-rows .search-row {
  height: 80px;
  padding: 10px;
}

.statistic-table-rows .search-row:last-child {
  margin-bottom: 8px;
}

.statistic-table-rows .search-row .search-row-name {
  font-size: 14px;
}

.statistic-table-rows .rows-abstract {
  height: 40px;
  width: calc(100% - 20px);
  margin: 0 10px;
  border-bottom: 2px solid #ededed;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.statistic-table-rows .rows-abstract .rows-count {
  display: inline-block;
  height: 100%;
  max-width: 70%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  line-height: 42px;
  color: #aaa;;
}

.statistic-table-rows .rows-abstract .rows-hide {
  display: inline-block;
  width: 40px;
  height: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  line-height: 42px;
  text-align: center;
  color: #F09F3F;
}

.statistic-table-rows .rows-abstract .rows-hide:hover {
  cursor: pointer;
}