.groupbys-dialog .groupbys-list {
  min-height: 120px;
  max-height: 300px;
  padding: 15px;
}

.groupbys-dialog .empty-groupbys-list {
  padding: .25rem;
  font-size: 14px;
  opacity: .5;
}

.groupbys-dialog .group-item {
  display: flex;
  align-items: center;
  padding: .25rem 0;
}

.groupbys-dialog .condition {
  display: flex;
  flex: 1;
}

.groupbys-dialog .group-column {
  width: 150px;
}

.groupbys-dialog .header-icon .dtable-font {
  display: inline-block;
  margin-right: .25rem;
  font-size: 12px;
  color: rgba(116, 116, 116, 1);
}

.groupbys-dialog .option:hover .header-icon .dtable-font {
  color: #fff;
}

.groupbys-dialog .group-count-type,
.groupbys-dialog .group-predicate {
  width: 100px;
  margin-left: .5rem;
}

.groupbys-dialog .delete-group {
  width: 12px;
  height: 20px;
  margin-left: 1rem;
  text-align: center;
}

.groupbys-dialog .delete-group:hover {
  cursor: pointer;
}

.groupbys-dialog .dtable-icon-fork-number {
  display: inline-block;
  font-size: 12px;
  color: #949494;
}

.groupbys-dialog .add-button-container {
  width: 100%;
  background-color: #fff;
  height: 38px;
  line-height: 38px;
  border-top: 1px solid #ededed;
}

.groupbys-dialog .add-button-container {
  padding: 0 1rem;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
}