.rc-calendar-date {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  line-height: 30px;
}
.rc-calendar-date-panel {
  width: 253px;
}
.rc-calendar .rc-calendar-selected-day .rc-calendar-date {
  background-color: #FcEcD9;
}
.rc-calendar .rc-calendar-date:hover,
.rc-calendar .rc-calendar-year-panel-year:hover,
.rc-calendar .rc-calendar-month-panel-cell .rc-calendar-month-panel-month:hover {
  background-color: #FcEcD9;
}
.rc-calendar .rc-calendar-selected-date .rc-calendar-date,
.rc-calendar .rc-calendar-selected-date .rc-calendar-date:hover,
.rc-calendar .rc-calendar-year-panel-selected-cell .rc-calendar-year-panel-year,
.rc-calendar .rc-calendar-month-panel-selected-cell .rc-calendar-month-panel-month {
  background-color: #f09f3f;
}
.rc-calendar .rc-calendar-next-year-btn:not([href]):not([tabindex]),
.rc-calendar .rc-calendar-prev-year-btn:not([href]):not([tabindex]),
.rc-calendar .rc-calendar-next-month-btn:not([href]):not([tabindex]),
.rc-calendar .rc-calendar-prev-month-btn:not([href]):not([tabindex]) {
  color: #999;
}
.rc-calendar .rc-calendar-next-year-btn:not([href]):not([tabindex]):hover,
.rc-calendar .rc-calendar-prev-year-btn:not([href]):not([tabindex]):hover,
.rc-calendar .rc-calendar-next-month-btn:not([href]):not([tabindex]):hover,
.rc-calendar .rc-calendar-prev-month-btn:not([href]):not([tabindex]):hover {
  color: #666;
}
.rc-calendar .rc-calendar-today .rc-calendar-date {
  border: none;
  position: relative;  
}
.rc-calendar .rc-calendar-today .rc-calendar-date::after {
  content: '';
  background: #f09f3f;
  position: absolute;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  left: 45%;
  bottom: 0%;
  display: inline-block;
}
.rc-calendar .rdg-editor-container input.editor-main[readonly] {
  background-color: #fff;
}
.rc-calendar-time-picker {
  z-index: 1;
}
.rc-calendar-right-panel-body ul li.highlight {
  margin: 0;
  border: none;
  background-color: #fff;
  font-size: 12px;
}
.date-picker-container {
  box-sizing: border-box;
  position: relative;
  display: block;
  line-height: 1.5;
  margin-bottom: 22px;
}
.date-picker-container-row-expand {
  margin-bottom: 0;
  width: 320px;
}
