.links-editor-list {
  width: 300px;
  padding: 10px 0;
}

.links-editor-list .links-container {
  max-height: 200px;
  padding: 10px;
  overflow: auto;
}

.links-editor-list .search-links {
  padding: 0 10px;
}

.links-editor-list .search-links input {
  height: 28px;
}

.links-editor-list .link-item {
  display: flex;
  align-items: center;
  padding-left: 0;
  padding-right: 0;
}

.links-editor-list .link-item:hover {
  background: none;
}

.links-editor-list .link-container {
  display: flex;
  align-items: center;
  width: 100%;
  height: 30px;
}

.links-editor-list .link-container:hover {
  cursor: pointer;
}

.links-editor-list .link {
  flex: 1;
  cursor: pointer;
}

.links-formmatter .link-name,
.links-editor-list .link-name {
  margin-left: 4px;
  max-width: 140px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.links-editor-list .link-check-icon {
  width: 20px;
  text-align: center;
}

.links-editor-list .link-check-icon .dtable-font {
  font-size: 12px;
  color: #798d99;
}

.links-editor-list .none-search-result {
  font-size: 14px;
  opacity: .5;
  display: inline-block;
}

.links-formmatter {
  display: inline-flex;
  align-items: center;
  overflow: hidden;
  flex-wrap: nowrap;
}

.links-formmatter .formmatter-show {
  display: inline-flex;
  align-items: center;
  min-height: 28px;
}

.link-container .link, .links-formmatter .link {
  display: inline-flex;
  height: 20px;
  margin-right: 10px;
  padding: 0 8px 0 2px;
  font-size: 13px;
  max-width: 100%;
}

.link-container .link-name {
  background: #eceff4;
  padding: 1px 8px;
  border-radius: 3px;
}

.links-formmatter .link {
  background: #eceff4;
  border-radius: 3px;
}

.links-formmatter .remove-container {
  width: 16px;
  text-align: right;
}

.links-formmatter .remove-icon {
  display: inline-block;
  position: relative;
  right: -2px;
  width: 14px;
  height: 20px;
}

.links-formmatter .remove-icon .dtable-font {
  display: inline-block;
  font-size: 12px;
  color: #909090;
  transform: scale(.8)
}