.thead-select-option-body {
  padding: 0;
}

.thead-select-option-body .mt-2 {
  min-height: 120px;
  padding: 1rem;
  box-sizing: border-box;
}

.select-options-list {
  margin: 1rem 0;
  padding: 0;
}

.option-item-container {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 40px;
  padding: 0 1rem;
}

.option-container {
  cursor: pointer;
  background-color: #f5f5f5;
}

.option-can-drop {
  background-color: #e9e9e9;
}

.multiple-option-item-cantainer {
  margin-bottom: .5rem;
  height: 2.25rem;
  padding: 0;
}

.option-item-container .rdg-drag-option-handle .dtable-icon-drag {
  color: #c2c2c2 !important;
}

.option-item-container .rdg-drag-option-handle:hover {
  cursor: move;
  cursor: grab;
}

.option-item-container .option-item {
  flex: 1 1;
  border-radius: .25rem;
  display: flex;
  align-items: center;
  overflow: hidden;
  margin-left: 10px;
}

.option-item-container .multiple-option-item {
  height: 40px;
  background-color: #ccc;
  margin-left: 0px !important;
}

.option-item-container .multiple-option-item .dtable-icon-check-mark {
  position: absolute;
  top: 0;
  right: 0;
}

.dialog-add-new-option {
  height: 40px;
  border-top: 1px solid #e9ecef;
  padding: 0 1rem;
  box-sizing: border-box;
  line-height: 40px;
}

.dialog-add-new-option .border-bottom {
  border-bottom: none !important;
}

.option-item-container .option-item .option-name {
  height: 20px;
  padding-left: .5rem;
  padding: 0 10px;
  border-radius: 10px;
  line-height: 20px;
  font-size: 13px;
  max-width: 400px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.option-item-container .option-edit {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: .5rem;
  width: 2.25rem;
  height: 2.25rem;
  border-radius: .25rem;
  color: #798d99;
}

.selected-option {
  width: 0.5rem;
  align-self: stretch;
  background-color: #bbb;
}

.thead-single-select .colorinput-color,
.tbody-single-select .colorinput-color {
  width: 2rem;
  height: 2rem;
}

.thead-single-select .colorinput-color:before,
.tbody-single-select .colorinput-color:before {
  display: none;
}

.thead-single-select .colorinput-color i,
.tbody-single-select .colorinput-color i {
  width: 2rem;
  height: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #7f7f7f;
}

.tbody-single-select .dtable-icon-return,
.thead-single-select .dtable-icon-return {
  color: #7f7f7f;
  font-size: 14px;
}

.thead-multiple-select .colorinput-color,
.tbody-multiple-select .colorinput-color {
  width: 2rem;
  height: 2rem;
}

.thead-multiple-select .colorinput-input:checked ~ .colorinput-color:before,
.tbody-multiple-select .colorinput-input:checked ~ .colorinput-color:before {
  display: none;
}

.thead-multiple-select .colorinput-color i,
.tbody-multiple-select .colorinput-color i {
  width: 2rem;
  height: 2rem;
  background: none;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #7f7f7f;
}

.thead-single-select .col-auto, .tbody-single-select .col-auto,
.tbody-multiple-select .col-auto, .thead-multiple-select .col-auto {
  padding-left: 3px;
}

.tbody-multiple-select .dtable-icon-return,
.thead-multiple-select .dtable-icon-return {
  color: #7f7f7f;
  font-size: 14px;
}

.option-item-container .more-option-operation {
  position: relative;
}

.option-item-container .more-option-operation .dtable-icon-more-level {
  color: #c2c2c2 !important;
}

.option-item-container .more-option-operation:hover .dtable-icon-more-level {
  color: #212529 !important;
}

.option-item-container .more-option-operation .option-operation-dropdownmenu {
  position: absolute;
  left: 0;
}

.option-operation-dropdownmenu .dropdown-item:hover {
  background-color: #f0f0f0;
  color: #6e7687;
  cursor: pointer;
}

.option-operation-dropdownmenu .dtable-font {
  font-size: 14px;
  margin-right: 10px;
  color: #949494;
}