.filters-dialog .filters-list {
  min-height: 120px;
  max-height: 100%;
  padding: 15px;
}

.filters-list .empty-filters-list {
  padding: .25rem;
  font-size: 14px;
  opacity: .5;
}

.filters-list .filter-item {
  display: flex;
  align-items: center;
  padding: .25rem 0;
}

.filter-item .condition {
  display: flex;
  flex: 1;
}

.filter-item .condition>div {
  height: 38px;
  line-height: 38px;
  margin-left: .5rem;
}

.filter-item .condition>div:first-child {
  margin-left: 0;
}

.filter-conjunction {
  width: 72px;
}

.filter-container {
  width : calc(100% - 72px);
  display: flex;
}

.filter-container .dtable-select .selected-option-show {
  overflow: visible;
}

.filters-dialog .dtable-select .selected-option {
  width: auto;
}

.filters-dialog .dtable-select .dtable-icon-drop-down {
  margin-left: .5rem;
}

.filters-dialog .selected-conjunction-show {
  padding: 0 10px;
  color: #999;
}

.filter-column .option:hover .header-icon .dtable-font {
  color: #fff;
}

.filter-term .selector-single-select .option:hover {
  color: #212529;
  background-color: #f7f7f7;
}

.filter-term .selector-collaborator .dtable-icon-drop-down {
  padding-left: 5px;
}

.filter-term .selector-collaborator .selected-option-show {
  text-overflow: unset;
}

.filter-term .selector-multiple-select .option:hover,
.filter-term .selector-collaborator .option:hover {
  color: #212529;
  background-color: #f7f7f7;
}

.single-select-option {
  display: inline-flex;
  text-align: center;
  width: min-content;
  max-width: 100px;
  line-height: 20px;
  margin: 6px 0px;
  border-radius: 10px;
  padding: 0px 10px;
  font-size: 13px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.multiple-select-option {
  display: inline-flex;
  text-align: center;
  width: min-content;
  max-width: 100px;
  line-height: 20px;
  margin: 6px 0px;
  border-radius: 10px;
  padding: 0px 10px;
  font-size: 13px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.filter-item .filter-term input {
  display: flex;
  width: 100%;
  height: 38px;
  background-color: #ffffff;
  padding-left: 8px;
  padding-right: 8px;
  color: #202428;
  outline: none;
  border: 1px solid rgba(0, 40, 100, 0.12);
  border-radius: 3px;
}

.filter-item .filter-term input:focus {
  color: #495057;
  background-color: #fff;
  border-color: #1991eb;
  outline: 0;
  box-shadow: 0 0 0 2px rgba(70, 127, 207, 0.25);
}

.filter-item .filter-term input[type='checkbox']:focus {
  border: 0;
  box-shadow: none;
}

.filters-list .delete-filter {
  width: 12px;
  height: 20px;
  margin-left: 1rem;
  text-align: center;
}

.filters-list .delete-filter:hover {
  cursor: pointer;
}

.delete-filter .dtable-icon-fork-number {
  display: inline-block;
  font-size: 12px;
  color: #949494;
}

.filters-dialog .multiple-option-name {
  display: flex;
  align-items: center;
}

.filters-dialog .multiple-check-icon,
.filters-dialog .collaborator-check-icon {
  display: inline-flex;
  width: 20px;
  text-align: center;
}

.filters-dialog .multiple-check-icon .dtable-icon-check-mark,
.filters-dialog .collaborator-check-icon .dtable-icon-check-mark {
  font-size: 12px;
  color: #798d99;
}

.filters-dialog .collaborator {
  display: inline-flex;
  align-items: center;
  height: 20px;
  margin-right: 10px;
  padding: 0 8px 0 2px;
  font-size: 13px;
  border-radius: 10px;
  background: #eaeaea;
}

.filters-dialog .collaborator-show {
  flex: 1;
}

.filters-dialog .collaborator-avatar {
  width: 16px;
  height: 16px;
  transform: translateY(-1px);
}

.filters-dialog .option .collaborator-avatar {
  transform: translateY(-2px);
}

.filters-dialog .collaborator-name {
  margin-left: 5px;
}

.filters-dialog .option-collaborator {
  display: flex;
}

.filters-dialog .collaborator-container {
  flex: 1;
}