.row-cell-selected .react-grid-Cell, .react-grid-Row.row-context-menu .react-grid-Cell {
  background-color: #f9f9f9;
}

.row-cell-selected .cell-highlight, .react-grid-Row.row-context-menu .cell-highlight {
  background-color:rgba(240, 159, 63, 0.6) !important;
}

.row-cell-selected .cell-current-highlight, .react-grid-Row.row-context-menu .cell-current-highlight {
  background-color:rgb(240, 159, 63) !important;
}

.react-grid-Row:hover .rdg-row-index {
  display: none;
}

.react-grid-Row:hover .rdg-actions-checkbox {
  opacity: 1;
}

.react-grid-Row:hover .rdg-drag-row-handle {
  cursor: move; /* fallback if grab cursor is unsupported */
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
  width: 12px;
  height: 30px;
  margin-left: 0px;
  background-image: url('../../assets/image/grippy_large.png');
  background-repeat: no-repeat;
}

.react-grid-Row.row-selected {
  background-color: #DBECFA;
}

.group-item .react-grid-Row.row-selected {
  width: calc(100% - 12px);
}

.react-grid-Row .row-selected {
  background-color: #DBECFA;
}

.react-grid-row-group .row-expand-icon:hover {
  color: #777777;
}

.react-grid-row-index {
  padding: 0 18px;
}

.rdg-row-index {
  display: block;
  text-align: left;
  padding-left: 20px;
}

.rdg-row-actions-cell {
  padding: 0px;
}

.rdg-actions-checkbox {
  opacity: 0;
  text-align: center;
}

.rdg-actions-checkbox.selected {
  opacity: 1;
}

.rdg-dragging {
  /*cursor: url(http://www.google.com/intl/en_ALL/mapfiles/closedhand.cur);*/
  cursor: -webkit-grabbing;
  cursor: -moz-grabbing;
  cursor: grabbing;
}

.rdg-dragged-row {
  border-bottom: 1px solid black
}

.react-grid-row-add:hover {
  background-color: #f9f9f9;
}

.row-filtered .react-grid-Cell {
  border-top: 1px solid red !important;
  border-bottom: 1px solid red !important;
}

.react-grid-Row.delete-animation {
  animation: delete-animation 0.3s ease-out;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
@keyframes delete-animation {
  from {}
  to {
    margin-top: 0;
  }
}

.react-grid-Row .filtered-row-message {
  color: #fff;
  padding: 2px 6px;
  font-size: 12px;
  font-weight: 400;
  font-weight: normal;
  position: absolute;
  left: -1px;
  white-space: nowrap;
  background: #ff6f2c;
  z-index: 3;
}
